<template>
	<input
		:id="field_id"
		:name="field_id"
		v-model="value"
		:placeholder="field_data.placeholder"
		:disabled="field_data.type_settings?.disabled || cena_disable"
		@blur="transform_value"
		:maxlength="html_attributes(true)"
		:minlength="html_attributes(false)"
		type="text"
		class="form-control"
		:class="transform_class"
	>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import podlazie_text_mixin from '@/components/forms/dynamic_input_mixins/podlazie_text_mixin.js';
	import cena_text_mixin from '@/components/forms/dynamic_input_mixins/cena_text_mixin.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed'],
		mixins: [podlazie_text_mixin, cena_text_mixin],
		data() {
			return {
				cena_disable: false,
			}
		},
		created() {
		},
		methods: {
			transform_value()
			{
				if (typeof this.value === 'string' && this.value !== this.value.trim()) {
					this.value = this.value.trim();
				}
				if (this.field_data.type_settings.transform === 'uppercase') {
					this.value = this.value.toUpperCase();
				} else if (this.field_data.type_settings.transform === 'capitalize') {
					// first character upper, rest of string left alone
					this.value = this.value.charAt(0).toUpperCase() + this.value.slice(1);
				}
				if (this.emit_name !== undefined) {
					this.$emit(this.emit_name);
				}
			},
			html_attributes(min_max)
			{
				// default for max is 50
				if (min_max) {
					return this.field_data.attributes?.maxlength || 50;
				}
				// default for min is 0
				else {
					return this.field_data.attributes?.minlength || 0;
				}
			},
		},
		computed: {
			transform_class()
			{
				if (this.field_data.type_settings.transform === 'uppercase') {
					return 'text_uppercase';
				} else if (this.field_data.type_settings.transform === 'capitalize') {
					return 'text_capitalize';
				} else {
					return '';
				}
			},
			value:
			{
				get() {
					if (this.emit_name !== undefined) {
						return STATE_params().get_user_input(this.field_id);
					}
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
				},
			},
		},
		watch: {
		},
	}
</script>
