import STATE_params from '@/store/STATE_params.js';
import Date_helpers from '@/components/forms/advanced_inputs/Date_helpers.js';

let mixin = {
	props: {
	},
	components: {
	},
	emits: [],
	data() {
		return {
			ranges: [
				{ from: 'cestovne_datum_odchodu', till: 'cestovne_datum_prichodu', },
				{ from: 'cudzinci_zaciatokpoistenia', till: 'cudzinci_koniecpoistenia', },
			],
			disable_before: {
				day: 0,
				month: 0,
				year: 0,
			},
		}
	},
	created() {
		// define disable_before, today if it is "from", "from" if it is "till"
		let i, len = this.ranges.length;
		for (i = 0; i < len; i++) {
			if (this.field_id === this.ranges[i].from) {
				this.disable_before = Date_helpers.default_shown('');
			}
			if (this.field_id === this.ranges[i].till) {
				this.disable_before = Date_helpers.default_shown(this.parent_value);
			}
		}
	},
	methods: {
		is_before_disabled_day(day)
		{
			if (day === 0 || this.disable_before.year === 0)
			{
				return false;
			}
			if (
				this.shown.year < this.disable_before.year
				||
				(
					this.shown.year === this.disable_before.year
					&&
					this.shown.month < this.disable_before.month
				)
				||
				(
					this.shown.year === this.disable_before.year
					&&
					this.shown.month === this.disable_before.month
					&&
					day < this.disable_before.day
				)
			)
			{
				return true;
			}
			return false;
		},
		is_before_disabled_month(month)
		{
			if (this.disable_before.year === 0) {
				return false;
			}
			if (
				this.shown.year < this.disable_before.year
				||
				(
					this.shown.year === this.disable_before.year
					&&
					month < this.disable_before.month
				)
			) {
				return true;
			}
			return false;
		},
		is_before_disabled_year(year)
		{
			if (this.disable_before.year === 0) {
				return false;
			}
			if (
				year < this.disable_before.year
			) {
				return true;
			}
			return false;
		},
		range_child_cant_start_before_parents_value()
		{
			// this.value is baseline
			if (this.parent_value === null) {
				return this.value;
			}
			// if this input wanted to show date that is now disabled, open on first disabled date instead
			if (
				this.picked.year < this.disable_before.year
				||
				(
					this.picked.year === this.disable_before.year
					&&
					this.picked.month < this.disable_before.month
				)
			) {
				// open calendar in parent_value
				return this.parent_value;
			}
			// if its later use own value
			return this.value;
		},
	},
	computed: {
		parent_value()
		{
			// reacts on ranges.from value change and set it as starting point
			let i, len = this.ranges.length;
			for (i = 0; i < len; i++) {
				if (this.field_id === this.ranges[i].till) {
					return STATE_params().get_form_value(this.ranges[i].from);
				}
				return null;
			}
		},
	},
	watch: {
		parent_value()
		{
			if (this.parent_value === null) {
				return;
			}
			// dates before parents picked cant be selected
			this.disable_before = Date_helpers.default_shown(this.parent_value);
		},
	},
};

export default mixin;
