<template>
	<div
		:class="columns"
		:data-hide="hide"
		:data-cy="'input_wrap-' + field_id"
	>
		<!-- errors NP -->
		<Errors v-if="!zeteo_wrap" :has_error="has_error" />
		<div
			:class="[required, blank, label_size, error_class, bootstrap_classes]"
			class="input-block"
		>
			<!-- label -->
			<div
				v-if="label"
				class="input-label"
			>
				{{ label }}
			</div>
			<!-- tooltip ZT -->
			<Tooltip v-if="zeteo_wrap" :tooltip="tooltip" />
			<!-- input block -->
			<div
				:class="input_block_class"
				class="input-area"
				:data-cy="'input-' + field_id"
			>
				<!-- input -->
				<Input_switcher
					v-for="(value, key) in inputs"
					:key="key"
					:field_data="value"
					:field_id="key"
				/>
			</div>
			<!-- errors ZT -->
			<Errors v-if="zeteo_wrap" :has_error="has_error" />
			<!-- tooltip NP -->
			<Tooltip v-if="!zeteo_wrap" :tooltip="tooltip" />
		</div>
	</div>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import Input_switcher from '@/components/forms/Input_switcher.vue';
	import Show_hide from '@/logic/Show_hide.js';
	import STATE_forms from '@/store/STATE_forms.js';
	import Functions from '@/logic/Functions.js';
	import Errors from '@/components/forms/wrap_parts/Errors.vue';
	import Tooltip from '@/components/forms/wrap_parts/Tooltip.vue';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
			Input_switcher,
			Errors,
			Tooltip,
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			inputs()
			{
				// separate single inputs from block inputs
				let inputs = {};

				if (this.field_data.elements === undefined) {

					// normal way where every field contain single input
					inputs[this.field_id] = this.field_data;

				} else {

					// some fields are blocks of multiple inputs
					for (let order in this.field_data.elements) { if (!this.field_data.elements.hasOwnProperty(order)) { continue; }

						// because order is now '2016001' and not 'zp_osoba1_poistenie_ks', which is inside
						let element, key;
						if (Functions.contains_only_digits(order))
						{
							key = Functions.first_key(this.field_data.elements[order]);
							element = this.field_data.elements[order][key];
						}
						else
						{
							// backwards compatibility, remove after _block order goes to production BE
							key = order;
							element = this.field_data.elements[key];
						}

						// Show_hide inside block
						let input_format = { [key]: element };
						if (Show_hide.visibility_boolean(input_format)) {
							inputs[key] = element;
						}

					}

				}

				// returns mostly one input, but multiple for block
				return inputs;
			},
			has_error()
			{
				// validate stores errors into state
				let errors = STATE_forms().get_errors();

				// messages in array
				let found = [];

				// loop inputs in case of block
				for (let id in this.inputs) { if (!this.inputs.hasOwnProperty(id)) { continue; }

					// skip if doesnt occur in errors from validate
					if (!errors.hasOwnProperty(id)) {
						continue;
					}
					/* OLD WAY, PROBABLY JUST BUGGED, REMOVE AFTER WHILE, 03.05.2023
					let error = '';
					// could be object
					if (typeof errors[id] === 'object') {
						error = Functions.first_value(errors[id]);
					}
					// or array
					else {
						error = errors[id][0];
					}*/

					// each input could have multiple messages
					for (let key in errors[id]) { if (!errors[id].hasOwnProperty(key)) { continue; }
						// save string to array
						found.push(errors[id][key]);
					}

				}

				// array of strings
				return found;
			},
			error_class()
			{
				if (this.zeteo_wrap || this.has_error.length === 0) {
					return '';
				} else {
					return 'validation-error';
				}
			},
			bootstrap_classes()
			{
				if (this.zeteo_wrap) {
					return 'input-block--alt';
				} else {
					return 'd-flex align-items-start align-items-sm-center flex-column';
				}
			},
			zeteo_wrap()
			{
				return STATE_core().is_zeteo_skin();
			},
			columns()
			{
				// zeteo skin step1, but not blocks or plaintexts
				if (
					this.zeteo_wrap
					&&
					STATE_core().current_step() === 1
					&&
					this.field_data.elements === undefined
					&&
					this.field_data.type !== 'plaintext'
				) {
					return 'col-sm-6 col-lg-3';
				}
				let size = '';
				// block
				if (this.field_data.block_settings !== undefined && this.field_data.block_settings !== null) {
					size = this.field_data.block_settings.size;
				}
				// single
				else if (this.field_data.type_settings !== undefined && this.field_data.type_settings !== null) {
					size = this.field_data.type_settings.size;
				}
				// default
				else {
					return 'col-12';
				}
				// translate keywords to BS classes
				if (size === 'full') {
					return 'col-12';
				}
				else if (size === 'half') {
					return 'col-lg-6';
				}
				else if (size === 'third') {
					return 'col-lg-4';
				}
				else {
					return 'col-12';
				}
			},
			required()
			{
				return '';
				// required is temporarily turned off on client's request
				// return 'input-required';
			},
			blank()
			{
				// does not have gray background
				if (this.field_data.type_settings?.design === 'blank') {
					return 'input-block-blank';
				} else {
					return '';
				}
			},
			hide()
			{
				// show hide condition in dataset, only for easier debug
				if (this.field_data.elements === undefined) {
					return this.field_data['data-hide-condition'];
				} else {
					return this.field_data['block_hide_condition'];
				}
			},
			label_size()
			{
				if (this.field_data.type_settings?.label_size === 'fullwidth') {
					return 'input-label-full';
				} else {
					return 'flex-sm-row';
				}
			},
			label()
			{
				// simple checkboxes doesnt have label
				if (this.field_data.type === 'checkbox') {
					return false;
				}
				// block checkbox_collapse doesnt have label
				else if (this.field_data.block_settings?.block_layout === 'checkbox_collapse') {
					return false;
				}
				// every other input has label
				else {
					// simple fields uses own label
					if (this.field_data.elements === undefined) {
						return this.field_data.label;
					}
					// blocks uses block label
					else {
						return this.field_data.block_label;
					}
				}
			},
			input_block_class()
			{
				// normal way where every field contain single input
				if (this.field_data.elements === undefined) {
					return '';
				}
				// some fields are blocks of multiple inputs
				// .input-mixed determine the input is block type, few block layouts adds additional class
				else {
					if (this.field_data.block_settings.block_layout === 'select_text') {
						return 'input-mixed input-select-text';
					}
					else if (this.field_data.block_settings.block_layout === 'checkbox_collapse') {
						return 'input-mixed input-onoff';
					}
					else {
						return 'input-mixed';
					}
				}
			},
			tooltip()
			{
				// replacing line break so they could easily use enters in editors
				// replacing &quot; because the couldnt use double quotes in old system
				// single
				if (this.field_data?.description) {
					return this.field_data.description.replace(/\r\n/g, '<br>').replace(/&quot;/g, '"');
				}
				// block
				else if (this.field_data?.block_input_hint) {
					return this.field_data.block_input_hint.replace(/\r\n/g, '<br>').replace(/&quot;/g, '"');
				}
				// empty
				else {
					return false;
				}
			},
		},
		watch: {
		},
	}

</script>
