<template>
	<div
		:class="is_disabled ? 'the_form_is_disabled' : ''"
		id="filters_form"
		data-cy="filters"
	>
		<div
			@click="toggle"
			class="filter_head text-center"
		>
			<p class="filter_toggle">
				<span class="i_adjust"></span>
				filter
			</p>
		</div>
		<div
			v-show="opened"
			class="product_selection"
		>
			<!-- inputs -->
			<div class="row">
				<div
					v-for="(label, id) in got_products"
					class="col-sm-6 col-md-4"
				>
					<div class="form-check">
						<input
							:id="id"
							:name="id"
							v-model="values[id]"
							:disabled="disable_in_filter(id)"
							true-value="1"
							false-value="0"
							type="checkbox"
							class="form-check-input"
						>
						<label
							:for="id"
							class="form-check-label"
						>
							{{ label }}
						</label>
					</div>
				</div>
			</div>

			<!-- submit button -->
			<div class="text-center">
				<button
					@click="filter_submit"
					class="btn btn-primary btn-m mt-3"
				>
					Vyber produkty
				</button>
			</div>

			<!-- error -->
			<div
				v-show="error"
				class="text-center"
			>
				<p class="mt-2 mb-0 text-warning">Minimálne jeden z produktov musí byť vybraný.</p>
			</div>

		</div>
		<!-- TEMPORARY DISABLED FEATURE, 18.07.2022
		<form id="product_options" class="col-6">
			<p class="text-center">Filter možností</p>
			<div id="filters_options">--
				inserted via js
			</div>
			<div class="text-center">
				<input class="btn btn-primary mt-4" type="submit" name="submit" value="Filtruj produkty">
			</div>
		</form>
		 -->
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import Group_user_form from '@/logic/requests/Group_user_form.js';

	export default {
		props: {
			got_products: Object,
			filtered_array: Array,
			disabled_from_calculate: Array,
			global_disabled: Boolean,
		},
		components: {
		},
		emits: ['calculate_all_products'],
		data() {
			return {
				opened: false,
				values: {
					// 245: '1',
					// ...
				},
				error: false,
				validate_running: false,
			}
		},
		created() {
			// create v-model values from getproducts api
			for (let key in this.got_products) { if (!this.got_products.hasOwnProperty(key)) { continue; }

				// empty filtered shows all checked
				// otherwise check if exists in filtered_array
				if (
					this.filtered_array.length === 0 ||
					this.filtered_array.indexOf(parseInt(key, 10)) > -1
				) {
					this.values[key] = '1';
				} else {
					this.values[key] = '0';
				}

			}
		},
		methods: {
			toggle()
			{
				this.opened = !this.opened;
			},
			disable_in_filter(id)
			{
				id = parseInt(id, 10);
				return this.disabled_from_calculate.indexOf(id) > -1;
			},
			filter_submit()
			{
				// gather checked and validate
				let ids = [];
				for (let key in this.values) { if (!this.values.hasOwnProperty(key)) { continue; }

					// checked in v-model
					if (this.values[key] === '1') {
						ids.push(key);
					}

				}

				// error if none checked
				if (ids.length === 0) {
					this.error = true;
					return;
				}
				else {
					this.error = false;
				}

				// all checked means all are displayed, therefor none is filtered
				if (ids.length === Object.keys(this.got_products).length) {
					ids = [];
				}

				// loading
				this.validate_running = true;

				// session stores this value as string of numbers '217,219'
				let form_data = 'filtered_products=' + ids.join(',');

				// validate api
				Group_user_form.validate_global_form(form_data).then(
					(response) => {

						// loading
						this.validate_running = false;

						// fallback
						if (response.status !== 200) {
							return;
						}

						// update in state
						STATE_params().set_filtered_products(ids.join(','));

						// new calculate for everyone
						this.$emit('calculate_all_products');

					}
				);
			},
		},
		computed: {
			is_disabled()
			{
				return this.global_disabled || this.validate_running;
			},
		},
		watch: {
		},
	}
</script>

<style lang="scss" scoped>
	.the_form_is_disabled {
		pointer-events: none;
		opacity: 0.5;
	}
</style>
