<template>

	<!-- not shown in technical steps like error or maintenance -->
	<div
		v-if="not_shown_in_technical_steps"
		class="row gx-0 step_progress_alt"
	>
		<div class="col-lg-8">
			<div class="step_progress_alt_steps" data-cy="progress_bar">

				<div
					:class="step_info_class(1)"
					class="step_info"
				>
					<div class="step_number">1.</div>
					<div class="step_title">Parametre<br>poistenia</div>
				</div>
				<div
					:class="step_info_class(2)"
					class="step_info"
				>
					<div class="step_number">2.</div>
					<div class="step_title">Porovnanie<br>ponúk</div>
				</div>
				<div
					:class="step_info_class(3)"
					class="step_info"
				>
					<div class="step_number">3.</div>
					<div class="step_title">Doplňujúce<br>údaje</div>
				</div>
				<div
					:class="step_info_class(4)"
					class="step_info"
				>
					<div class="step_number">4.</div>
					<div class="step_title">Rekapitulácia</div>
				</div>
				<div
					:class="step_info_class(5)"
					class="step_info"
				>
					<div class="step_number">5.</div>
					<div class="step_title">Dokončenie</div>
				</div>

			</div>
		</div>
	</div>

</template>

<script>
	import STATE_core from '@/store/STATE_core.js';

	export default {
		props: {
			show_back: Boolean,
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			step_info_class(int)
			{
				if (int < this.step) {
					return 'completed';
				}
				else if (int === this.step) {
					return 'current';
				}
				else {
					return '';
				}
			},
		},
		computed: {
			step()
			{
				return STATE_core().current_step();
			},
			not_shown_in_technical_steps()
			{
				return this.step < 10;
			},
		},
		watch: {
		},
	}
</script>
