<template>

	<!-- step4 -->
	<div
		v-if="step_html === '4'"
		class="docs_download"
		data-cy="documents"
	>
		<h3>
			Predzmluvná dokumentácia
		</h3>
		<p>
			Dokumenty s ktorými je klient povinný sa oboznámiť
		</p>
		<div class="docs_static">
			<p>
				Produktové dokumenty
			</p>
			<!-- static_docs -->
			<Document_download
				v-for="(value, key) in static_docs"
				:doc="value"
			/>
		</div>
		<div class="docs_session">
			<p>
				Dynamické dokumenty
			</p>
			<!-- session_docs -->
			<Document_download
				v-for="(value, key) in session_docs"
				:doc="value"
			/>
		</div>
	</div>

	<!-- step5 -->
	<div
		v-if="step_html === '5'"
		class="row docs_download"
		data-cy="documents"
	>
		<div class="col-12 col-md-6">
			<div class="s-box-body">
				<p>
					Produktové dokumenty
				</p>
				<!-- static_docs -->
				<Document_download
					v-for="(value, key) in static_docs"
					:doc="value"
				/>
			</div>
		</div>
		<div class="col-12 col-md-6">
			<div class="s-box-body">
				<p>
					Dynamické dokumenty
				</p>
				<!-- session_docs -->
				<Document_download
					v-for="(value, key) in session_docs"
					:doc="value"
				/>
			</div>
		</div>
	</div>

</template>

<script>
	import Get_static_docs from '@/logic/requests/Get_static_docs.js';
	import Get_session_docs from '@/logic/requests/Get_session_docs.js';
	import Document_download from '@/components/form_app/documents/Document_download.vue';
	import STATE_modals from '@/store/STATE_modals.js';

	export default {
		props: {
			start_doc_requests: Boolean,
			step_html: String, // 4, 5
		},
		components: {
			Document_download,
		},
		emits: ['all_docs_finished'],
		data() {
			return {
				static_docs: {},
				session_docs: {},

				turbo: {
					repeat_count: 0,
					repeat_delay_fast: 2 * 1000, // 2 seconds
					repeat_delay_slow: 10 * 1000, // 10 seconds
					repeat_diminish: 10, // 0-20 seconds
					repeat_max: 26, // 20-180 seconds
				},
			}
		},
		created() {
			// step4 tells this component that calculate is done and it can start
			if (this.start_doc_requests === true) {

				// start statics
				this.group_docs();

				// start sessions
				this.session_docs_turbo();

			}
		},
		methods: {
			group_docs()
			{
				// run once
				Get_static_docs.static_documents().then(
					(response) => {

						// save them aaand done
						this.static_docs = response.result.documents;

					}
				);
			},
			session_docs_turbo() {
				// repeats until done or reached maximum tries
				Get_session_docs.session_documents(false).then(
					(response) => {

						// save documents, unless request timed out or had some other error
						if (response.result?.data?.documents !== undefined) {
							this.session_docs = response.result.data.documents;
						}

						// check if webservice doc got error, and then show error and break the flow
						for (let key in this.session_docs) { if (!this.session_docs.hasOwnProperty(key)) { continue; }
							if (
								this.session_docs[key].name === 'ws-preview.tmp'
								&&
								this.session_docs[key].documentState === 'error'
							) {
								STATE_modals().store_modal_data(this.session_docs[key].errors);
								STATE_modals().open_it('Session_document_error');
								return;
							}
						}

						// is finished, show documents and confirm form
						if (response.result?.data?.processingFinished === true) {
							// let parent know we are done
							this.$emit('all_docs_finished');
							return;
						}

						// max repeats reached, die
						if (this.turbo.repeat_count > this.turbo.repeat_max) {
							console.log('getsessiondocuments reached maximum repeat_count');
							return;
						}

						// count every attempt
						this.turbo.repeat_count++;

						// first 10 repeats occures with 2s delay, later with 10s delay
						let delay = (this.turbo.repeat_count < this.turbo.repeat_diminish) ? this.turbo.repeat_delay_fast : this.turbo.repeat_delay_slow;

						// repeat request after delay
						setTimeout(
							() => { this.session_docs_turbo() },
							delay
						);

					}
				);
			},
		},
		computed: {
		},
		watch: {
			start_doc_requests()
			{
				// property could change anytime
				if (this.start_doc_requests === true) {

					// start statics
					this.group_docs();

					// start sessions
					this.session_docs_turbo();

				}
			},
		},
	}
</script>
