<template>
	<h4>Platba online</h4>
	<div class="gateways" data-cy="gateways" >
		<a
			v-for="(value, key) in gateways"
			v-show="value.url !== ''"
			:href="value.url"
			:id="key"
			:title="value.name"
			target="_blank"
		>
			<img
				:src="value.logo"
				:alt="value.name"
				width="127"
			>
		</a>
	</div>
</template>

<script>
	import Payment_requests from '@/logic/requests/Payment_requests.js';
	import STATE_core from '@/store/STATE_core.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
				gateways: {
					// id: {
					// 	name: 'Tatra Banka TatraPay',
					// 	logo: 'https://najpoistenie-test.studiotem.com/wp-content/themes/najpoistenie/images/loga-banky/ib-tatrapay.png',
					// 	url: 'https://paymentfinportal.cta.sk/pay-request?cid=25035&type=VUBPAY&amnt=105.84&curr=978&vs=2222000050&ru=https%3A%2F%2Fnajpoistenie.sk%2Fbanky%3Fpaymentid%3D5f3b2e1b-a241-475b-a5ff-92b1ad80833f%26token%3Df37baf3d-b082-48af-8cf8-ec4e3d2f2785&custip=192.210.1.22&custname=asd%40gmail.com&sign=15A7DF9269D9097F19A1D3A82AA26B16&email=asd%40gmail.com',
					// },
					// ...
				},
			}
		},
		created() {
			// getpaymentgateways
			Payment_requests.get_gateways().then(
				(response) => {

					// loop them all
					for (let id in response.result) { if (!response.result.hasOwnProperty(id)) { continue; }

						// skip without array key
						if (isNaN(id)) {
							continue;
						}

						// button attributes
						let name = response.result[id]?.name;
						let logo = response.result[id]?.url_logo;
						if (logo === null) {
							logo = '';
						} else if (logo.substring(0, 4) !== 'http') {
							logo = 'https://www.najpoistenie.sk/wp-content/themes/najpoistenie/images/loga-banky/' + logo + '.png';
						}

						// component format
						this.gateways[id] =	{
							logo: logo,
							name: name,
							url: '',
						};

						// create payment url
						Payment_requests.payment_create(id).then(
							(response) => {

								// error
								if (response.status !== 200) {
									console.log('requesting gateway URL failed with id: ' + id, response);
								}

								// success
								this.gateways[id].url = response.result.url;

								// if debug is enabled, replace gateway url for fake payment
								let debug = STATE_core().get_payment_data().payment_debug;
								if (debug) {
									this.debug_url_for_payment_gateway(id);
								}

							}
						);

					} // loop end

				}
			);
		},
		methods: {
			debug_url_for_payment_gateway(id)
			{
				/*
				EXAMPLE:
				https://paymentfinportal.cta.sk/pay-request?cid=44874&type=VUBPAY&amnt=75.51&curr=978&vs=7013378938&ru=https%3A%2F%2Fnajpoistenie.sk%2Fbanky%3Fpaymentid%3Dc7d6e1d7-f403-4e98-94af-fd9d558deac9%26token%3Dc955e715-b29c-4604-8ff0-03db8964ed63&custip=192.210.1.22&custname=qatrash%40intechon.sk&sign=019AE1523AF29B47D80021308E095882&email=qatrash%40intechon.sk
				*/

				let former_url = this.gateways[id].url;

				let fake_url = former_url?.replace('pay-request', 'pay-request-test');

				this.gateways[id].url = fake_url;
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
