<template>
	<Modal_body :close="false" title="Posielame e-mail klientovi" class="modal-signature"  >

		<div v-show="status === 'sending'">
			<p>Prebieha odosielanie</p>
			<Bouncer_loading />
		</div>

		<div v-show="status === 'success'">
			<p>Na klientov e-mail bola zaslaná zmluvná dokumentácia.</p>
			<p>Pre dokončenie, je potrebné, aby klient:</p>
			<ul>
				<li>otvoril zaslaný e-mail,</li>
				<li>oboznámil sa so zmluvnou dokumentáciou,</li>
				<li>klikol na odkaz, ktorý ho presmeruje na túto stránku,</li>
				<li>potvrdil zadaním SMS kódu, ktorý obdrží na svoje telefónne číslo, súhlas s obsahom všetkých dokumentov.</li>
			</ul>
			<button
				@click="clear"
				class="btn btn-primary"
			>
				Vymazať a otvoriť novú kalkuláciu
			</button>
		</div>

		<div v-show="status === 'fail'">
			<p>Odosielanie zlyhalo.</p>
			<p v-if="message" >
				{{ message }}
			</p>
		</div>

	</Modal_body>
</template>

<script>
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import Signatures from '@/logic/requests/Signatures.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import Functions from '@/logic/Functions.js';
	import Session_data from '@/logic/requests/Session_data.js';

	export default {
		props: {
		},
		components: {
			Bouncer_loading,
			Modal_body,
		},
		emits: [],
		data() {
			return {
				status: 'sending', // sending, fail, success
				message: '',
			}
		},
		created() {
			let checkboxes = STATE_modals().retrieve_modal_data();
			Signatures.offer_send(checkboxes).then(
				(response) => {
					if (response.status !== 200) {
						STATE_modals().close_them_all();
						return;
					}
					if (response.result.signature_required === 1) {
						this.status = 'success';
					} else {
						this.status = 'fail';
						this.message = JSON.stringify(response.result);
					}
				}
			).then(
				() => {
					// update signature state
					Session_data.set_data({ signature_state: 'client', });
				}
			);
		},
		methods: {
			clear()
			{
				Functions.clear_session();
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
