<template>
	<div
		class="osm_app_scope"
		:class="gray_background"
		data-cy="osm_app_dom_root"
	>
		<div
			class="container px-0"
			:class="[loading_class, skin, step_class]"
		>
			<div class="page_wrap">
				<Flow_sandbox v-if="mode === 'node_red'" />
				<View_switcher v-else-if="mode === 'form_app'" />
				<Docs_template v-else-if="mode === 'docs_landing'" />
				<p v-else >dont have systems for mode {{ mode }}</p>
			</div>
			<Modal_switcher />
		</div>
	</div>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import View_switcher from '@/components/form_app/View_switcher.vue';
	import Modal_switcher from '@/components/modals/Modal_switcher.vue';
	import Flow_sandbox from '@/components/flow/Flow_sandbox.vue';
	import Docs_template from '@/components/docs_landing/Docs_template.vue';
	import package_json from '@/../package.json';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';
	import Cookie_manager from '@/logic/Cookie_manager.js';
	import Functions from '@/logic/Functions.js';

	export default {
		components: {
			View_switcher,
			Modal_switcher,
			Flow_sandbox,
			Docs_template,
		},
		inject: ['config'],
		data() {
			return {
			}
		},
		created() {
			// inform user if session_token has wrong format
			if (Functions.invalid_session_uuid(this.config.session_token)) {
				STATE_modals().store_modal_data(this.config.session_token);
				STATE_modals().open_it('Invalid_uuid');
				return;
			}
			// save init config and log version
			this.config.version = package_json.version;
			STATE_core().save_the_config(this.config);
			STATE_params().save_agent_code(this.config.agent_code);
			STATE_params().set_filtered_products(this.config.filtered_products);
			console.log('osm_app:', package_json.version);
			Cookie_manager.init_manager();
		},
		mounted() {
			/*if (process.env.NODE_ENV === 'production') {
				window.addEventListener('beforeunload', function(event){ event.preventDefault(); });
			}*/
		},
		beforeDestroy () {
			/*if (process.env.NODE_ENV === 'production') {
				window.removeEventListener('beforeunload', function(event){ event.preventDefault(); });
			}*/
		},
		methods: {
		},
		computed: {
			skin()
			{
				return STATE_core().skin_class();
			},
			gray_background()
			{
				return STATE_core().is_zeteo_skin() ? 'gray_background' : '';
			},
			loading_class()
			{
				if (STATE_modals().is_loading()) {
					return 'loading';
				} else {
					return '';
				}
			},
			mode()
			{
				return STATE_core().get_run_mode();
			},
			step_class()
			{
				return (STATE_core().get_run_mode() === 'form_app') ? ('step' + STATE_core().current_step()) : '';
			},
		},
	}
</script>

<style scoped lang="scss">
	// fonts, got to be outside of deep scope
	@import '@/assets/styles/fonts.scss';

	// all app scoped styles
	.osm_app_scope::v-deep() {
		@import '@/style_imports.scss';
	}

	// loading is on same level as scope, so lets keep it outside
	.loading > .page_wrap {
		opacity: 0.6;
		pointer-events: none;
	}

	.gray_background {
		background: #F5F5F5;
	}
</style>
