<template>
	<div
		class="row input_tab"
		data-cy="input_tab"
	>
		<Wrap
			v-for="(field, order) in compute_active_tab"
			:field_data="field_data(field)"
			:field_id="field_id(field)"
		/>
	</div>
</template>

<script>
	import Wrap from '@/components/forms/Wrap.vue';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			tab_current: Number,
		},
		components: {
			Wrap,
		},
		data() {
			return {
			}
		},
		methods: {
			field_id(field) {
				// only one item inside
				return Functions.first_key(field);
			},
			field_data(field) {
				// only one item inside
				return Functions.first_value(field);
			},
		},
		computed: {
			compute_active_tab() {
				// filter inputs only for this tab
				// that passed hide condition
				return Functions.active_tab(this.tab_current);
			},
		},
	}
</script>
