<template>
	<div class="input-group input-counter">
		<div class="input-group-prepend">
			<a
				@click="substract()"
				class="input-group-btn btn-minus"
			></a>
		</div>
		<input
			:id="field_id"
			:name="field_id"
			v-model="value"
			maxlength="3"
			type="text"
			class="form-control"
		>
		<div class="input-group-append">
			<a
				@click="add()"
				class="input-group-btn btn-plus"
			></a>
		</div>
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
				min: 0,
				max: 9,
			}
		},
		created() {
			this.min = this.field_data.type_settings?.plus_minus_min || 0;
			this.max = this.field_data.type_settings?.plus_minus_max || 1000;
		},
		methods: {
			add()
			{
				if (this.value >= this.max) {
					return;
				}
				this.value++;
				STATE_params().set_form_value(this.field_id, this.value);
			},
			substract()
			{
				if (this.value <= this.min) {
					return;
				}
				this.value--;
				STATE_params().set_form_value(this.field_id, this.value);
			},
		},
		computed: {
			value:
			{
				get() {
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
				},
			},
		},
		watch: {
		},
	}
</script>
