<template>
	<div class="input-autocomplete">
		<input
			:id="field_id"
			:name="field_id"
			placeholder="-- vyberte --"
			v-model="displayed"
			@focus="shown = true"
			@blur="hide_with_delay"
			@keyup="remove_previous_value"
			@keydown.down="move_down"
			@keydown.up="move_up"
			@keydown.enter="enter_selection"
			type="text"
			class="form-control"
		/>
		<ul
			v-show="shown"
		>
			<li
				v-for="(item, order) in filtered_options"
				:class="order === selected ? 'active_autocomplete' : ''"
				@click="click_selection(order)"
			>
				{{ item.label }}
			</li>
		</ul>
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import Functions from '@/logic/Functions.js';
	import auto_select_mixin from '@/components/forms/dynamic_input_mixins/auto_select_mixin.js';
	import vybava_select_mixin from '@/components/forms/dynamic_input_mixins/vybava_select_mixin.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: [],
		mixins: [auto_select_mixin, vybava_select_mixin],
		data() {
			return {
				displayed: '',
				options: {
					// order: {
					// 	label: '',
					// 	value: '',
					// },
					// ...
				},
				selected: -1,
				shown: false,
			}
		},
		created() {
			// auto select mixin input is populated in mixin
			if (this.dynamic.hasOwnProperty(this.field_id)) {
				return;
			}

			// take enum and create nicer options object
			for (let order in this.field_data.enum) { if (!this.field_data.enum.hasOwnProperty(order)) { continue; }

				// received format
				let value = Functions.first_key(this.field_data.enum[order]);
				let label = Functions.first_value(this.field_data.enum[order]).label;

				// skip empty -- vyberte --
				if (value === '') {
					continue;
				}

				// simpler format
				this.options[order] = {
					value: value,
					label: label,
				};

				// set default value if received in session
				if (parseInt(value, 10) === parseInt(STATE_params().get_form_value(this.field_id), 10)) {
					this.displayed = label;
				}

			}
		},
		methods: {
			move_down()
			{
				this.selected++;
				if (this.selected > this.filtered_options.length - 1) {
					this.selected = 0;
				}
			},
			move_up()
			{
				this.selected--;
				if (this.selected < 0) {
					this.selected = this.filtered_options.length - 1;
				}
			},
			enter_selection()
			{
				// fallbacks
				if (this.selected === -1) {
					return;
				}
				if (this.filtered_options[this.selected] === undefined) {
					return;
				}

				// save value (not label) to state
				STATE_params().set_form_value(this.field_id, this.filtered_options[this.selected].value);

				// display label
				this.displayed = this.filtered_options[this.selected].label;

				// close options
				this.shown = false;

				// vybava only, adds label into session
				if (this.field_id === this.vybava_id) {
					let label = '';
					for (let order in this.field_data.enum) { if (!this.field_data.enum.hasOwnProperty(order)) { continue; }
						let key = Object.keys(this.field_data.enum[order])[0];
						if (key === event_value) {
							label = this.field_data.enum[order][key];
						}
					}
					STATE_params().set_form_value(this.vybava_additional_label, label);
				}
			},
			click_selection(order)
			{
				this.selected = order;
				this.enter_selection();
			},
			remove_previous_value()
			{
				// check if written value occurs in options
				let got_him = false;
				for (let order in this.options) { if (!this.options.hasOwnProperty(order)) { continue; }
					if (this.displayed === this.options[order].label) {
						got_him = true;
					}
				}

				// if not, remove last known value
				if (got_him === false) {
					STATE_params().delete_form_value(this.field_id);
					this.shown = true;
				}

				// when only one option matches, pick it automatically
				setTimeout(
					() => {
						if (Object.keys(this.filtered_options).length === 1) {
							this.selected = 0;
							this.enter_selection();
						}
					},
					1000
				);
			},
			hide_with_delay()
			{
				// clicking on list caused it to hide before click event occured
				setTimeout(
					() => { this.shown = false },
					200
				);
			},
		},
		computed: {
			filtered_options()
			{
				// find written value in options
				let filtered = [];
				for (let order in this.options) { if (!this.options.hasOwnProperty(order)) { continue; }

					// special letters dont have to match
					let haystack = Functions.slugify(this.options[order].label);
					let needle = Functions.slugify(this.displayed);

					// options that match filter
					if (haystack.indexOf(needle) !== -1) {
						filtered.push(this.options[order]);
					}

				}
				return filtered;
			},
			searchable_in_state_changed()
			{
				return STATE_params().get_form_value(this.field_id);
			},
		},
		watch: {
			searchable_in_state_changed()
			{
				// when value changes in state, f.e. from search query, update displayed by value
				let value = this.searchable_in_state_changed;

				// skip for clearing value
				if (value === '') {
					return;
				}

				// check all options
				for (let order in this.options) { if (!this.options.hasOwnProperty(order)) { continue; }

					// if value matches, use label
					if (this.options[order].value === value) {
						this.displayed = this.options[order].label;
						return;
					}

				}
			},
		},
	}
</script>
