<template>
	<div class="input-group multiple" ref="multiple_reference" >

		<!-- info -->
		<p
			:id="field_id"
			:name="field_id"
			@click="open_dropdown"
			class="form-control multiple_input"
		>
			{{ display_selected }}
		</p>

		<!-- dropdown part -->
		<div
			v-show="opened"
			class="multiple_dropdown"
		>

			<!-- controlls -->
			<div>
				<span @click="set_all" >
					Všetko
				</span>
				<span @click="set_none" >
					Nič
				</span>
				<span @click="accept" class="accept" >
					Potvrdiť
				</span>
			</div>

			<!-- options -->
			<div class="m-2">
				<div
					v-for="(item, order) in options"
					class="form-check my-2"
				>
					<input
						:id="field_id + '-' + order"
						:name="field_id + '-' + order"
						v-model="options[order].checked"
						type="checkbox"
						class="form-check-input"
					>
					<label
						:for="field_id + '-' + order"
						class="form-check-label"
					>
						{{ item.label }}
					</label>
				</div>
			</div>

		</div><!-- dropdown part -->
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed', 'recalculate_product'],
		data() {
			return {
				opened: false,
				delimiter: ',',
				options: {
					// 0: {
					// 	value: '1',
					// 	label: 'nafta',
					// 	checked: false,
					// },
				},
			}
		},
		created() {

			// source of value differs in product form and global form
			let value_source = '';
			if (this.emit_name !== undefined) {
				value_source = STATE_params().get_user_input(this.field_id);
			} else {
				value_source = STATE_params().get_form_value(this.field_id);
			}

			// value is delivered in string format with comma delimiter: "2,4,5"
			let input_value_array = value_source?.split(this.delimiter) || [];

			// create better format than enum
			for (let order in this.field_data.enum) { if (!this.field_data.enum.hasOwnProperty(order)) { continue; }

				// each item is one { key: "value", }
				let item = this.field_data.enum[order];

				this.options[order] = {
					value: Functions.first_key(item),
					label: Functions.first_value(item),
					checked: input_value_array.indexOf(Functions.first_key(item)) > -1,
				};

			}
		},
		methods: {
			open_dropdown()
			{
				// open
				this.opened = true;

				// listen if user wanted to close by clicking outside
				document.addEventListener('click', this.clicked_outside, false);
			},
			clicked_outside(event)
			{
				// where clicked
				const inside = this.$refs.multiple_reference.contains(event.target);

				// has to remove listener
				if (!inside) {
					document.removeEventListener('click', this.clicked_outside, false);
					this.accept();
				}
			},
			set_all()
			{
				for (let order in this.options) { if (!this.options.hasOwnProperty(order)) { continue; }
					this.options[order].checked = true;
				}
			},
			set_none()
			{
				for (let order in this.options) { if (!this.options.hasOwnProperty(order)) { continue; }
					this.options[order].checked = false;
				}
			},
			accept()
			{
				// just close, label is change with v-model
				this.opened = false;

				// save values into storage
				let sending = [];
				for (let order in this.options) { if (!this.options.hasOwnProperty(order)) { continue; }
					if (this.options[order].checked) {
						sending.push(this.options[order].value);
					}
				}

				// prepare sending for validate and launch emit if in produt/global
				sending = sending.join(this.delimiter);
				STATE_params().set_form_value(this.field_id, sending);
				if (this.emit_name !== undefined) {
					this.$emit(this.emit_name);
				}
			},
		},
		computed: {
			display_selected()
			{
				// dynamically change with v-model
				let displayed = [];
				for (let order in this.options) { if (!this.options.hasOwnProperty(order)) { continue; }
					if (this.options[order].checked) {
						displayed.push(this.options[order].label);
					}
				}

				// show placeholder if none selected
				if (displayed.length === 0) {
					return this.field_data.placeholder;
				}

				// show selected
				return displayed.join(', ');
			},
		},
		watch: {
		},
	}
</script>
