import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Credentails_requests
*/
class Credentails_requests
{
	/**
	 * add_page
	*/
	add_page(base64)
	{
		let post_object = { image: base64.split(',')?.[1] || '' };

		let requestParams = {
			action_name: 'id-scan/add-page',
			method: 'POST',
			url: STATE_core().endpoint_url() + 'api/online/id-scan/add-page/?' + Functions.add_token_as_parameter(),
			body: JSON.stringify(post_object),
			content_type: 'application/json',
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch((error) => { throw error; });
	}


	/**
	 * scan_groups
	*/
	scan_groups()
	{
		let requestParams = {
			action_name: 'id-scan/get-scan-groups',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'api/online/id-scan/get-scan-groups/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch((error) => { throw error; });
	}


	/**
	 * scan_group_documents
	*/
	scan_group_documents()
	{
		let requestParams = {
			action_name: 'id-scan/get-scan-group-documents',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'api/online/id-scan/get-scan-group-documents/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch((error) => { throw error; });
	}
}

export default new Credentails_requests;
