/**
 * Date_helpers
*/
class Date_helpers
{
	/**
	 * default_shown
	*/
	default_shown(value /*string in format 22.05.2023 or empty string*/)
	{
		/*
		^ asserts the start of the string.
		\d matches a digit (0-9).
		{1,2} specifies that the preceding pattern (digit) should occur between 1 and 2 times.
		\. matches the dot character literally.
		\d{4} matches exactly four digits.
		$ asserts the end of the string.
		*/
		let pattern = /^\d{1,2}\.\d{1,2}\.\d{4}$/;

		// set empty value if pattern does not match
		if (value !== '' && !pattern.test(value)) {
			value = '';
		}

		let now = {};
		if (value === '') {
			now = new Date();
		} else {
			value = value.split('.');
			now = new Date(value[2], value[1] - 1, value[0]);
		}

		return {
			day: now.getDate(),
			month: now.getMonth() + 1,
			year: now.getFullYear(),
			decade: (Math.floor(now.getFullYear() / 10) * 10) - 10,
		};
	}


	/**
	 * construct_days
	*/
	construct_days(year, month)
	{
		let last_date = new Date(year, month, 0).getDate();

		// all days in current month
		let days = [];
		var i;
		for (i = 1; i < last_date + 1; i++) {
			days.push(i);
		}

		// empty days in first week from previous month
		let first_day = new Date(year, month - 1, 1).getDay();
		let add_empty = null;
		/*
		0 ne  6
		1 po  0
		2 ut  1
		3 str 2
		4 stv 3
		5 pi  4
		6 so  5
		*/
		if (first_day === 0) {
			add_empty = first_day + 6;
		} else {
			add_empty = first_day - 1;
		}

		let last_of_previous = new Date(year, month - 1, 0).getDate();

		for (i = 0; i < add_empty; i++) {
			days.unshift(-last_of_previous + i);
		}

		// empty days in last week for next month
		let last_day = new Date(year, month, 0).getDay();
		let trail_empty = null;

		if (last_day === 0) {
			trail_empty = 0;
		} else {
			trail_empty = 7 - last_day;
		}
		let number_of_weeks = Math.ceil(days.length / 7);
		if (number_of_weeks < 6) {
			trail_empty += 7;
		}

		for (i = 0; i < trail_empty; i++) {
			days.push(100 + i + 1);
		}

		return days;
	}


	/**
	 * construct_week
	*/
	construct_week()
	{
		return [
			'Pon',
			'Ut',
			'Str',
			'Štv',
			'Pia',
			'Sob',
			'Ned',
		];
	}


	/**
	 * construct_months
	*/
	construct_months()
	{
		return {
			'Január': 1,
			'Február': 2,
			'Marec': 3,
			'Apríl': 4,
			'Máj': 5,
			'Jún': 6,
			'Júl': 7,
			'August': 8,
			'September': 9,
			'Október': 10,
			'November': 11,
			'December': 12,
		};
	}


	/**
	 * show_month_name
	*/
	show_month_name(order)
	{
		let moon = '';
		let moons = this.construct_months();
		for (let key in moons) { if (!moons.hasOwnProperty(key)) { continue; }
			if (moons[key] === order) {
				return key;
			}
		}
		return 'no month found';
	}


	/**
	 * construct_years
	*/
	construct_years(decade)
	{
		let years = [];
		let i;
		for (i = decade; i < decade + 30; i++) {
			years.push(i);
		}

		return years;
	}
}

export default new Date_helpers;
