<template>
	<!-- not shown in technical steps like error or maintenance -->
	<div
		v-if="not_shown_in_technical_steps"
		class="step_progress"
		data-cy="progress_bar"
	>

		<!-- point 1 -->
		<div
			:class="step_info_class(1)"
			class="step_info"
		>
			<div class="step_title">
				Základné údaje
			</div>
			<div
				@click="return_to_the_step(1)"
				class="step_number"
			>
				1
			</div>
			<div
				v-if="step === 1"
				class="step_tabs"
			>
				<span
					v-for="index in tab_count"
					@click="switch_tab(index)"
					:title="allow_tab_switch ? index : ''"
					:class="[step_tab_class(index), allow_tab_switch ? 'allow_tab_switch' : '']"
					v-show="tab_not_empty(index)"
				></span>
			</div>
		</div>

		<!-- line 1 -->
		<div
			:class="step_line_class(1)"
			class="step_line"
		></div>

		<!-- point 2 -->
		<div
			:class="step_info_class(2)"
			class="step_info"
		>
			<div class="step_title">
				Porovnanie a výber
			</div>
			<div
				@click="return_to_the_step(2)"
				class="step_number"
			>
				2
			</div>
		</div>

		<!-- line 2 -->
		<div
			:class="step_line_class(2)"
			class="step_line"
		></div>

		<!-- point 3 -->
		<div
			:class="step_info_class(3)"
			class="step_info"
		>
			<div class="step_title">
				Údaje do zmluvy
			</div>
			<div
				@click="return_to_the_step(3)"
				class="step_number"
			>
				3
			</div>
			<div
				v-if="step === 3"
				class="step_tabs"
			>
				<span
					v-for="index in tab_count"
					@click="switch_tab(index)"
					:title="allow_tab_switch ? index : ''"
					:class="[step_tab_class(index), allow_tab_switch ? 'allow_tab_switch' : '']"
					v-show="tab_not_empty(index)"
				></span>
			</div>
		</div>

		<!-- line 3 -->
		<div
			:class="step_line_class(3)"
			class="step_line"
		></div>

		<!-- point 4 -->
		<div
			:class="step_info_class(4)"
			class="step_info"
		>
			<div class="step_title">
				Zhrnutie
			</div>
			<div
				@click="return_to_the_step(4)"
				class="step_number"
			>
				4
			</div>
		</div>

		<!-- line 4 -->
		<div
			:class="step_line_class(4)"
			class="step_line"
		></div>

		<!-- point 5 -->
		<div
			:class="step_info_class(5)"
			class="step_info"
		>
			<div class="step_title">
				Hotovo!
			</div>
			<div
				@click="return_to_the_step(5)"
				class="step_number"
			>
				5
			</div>
		</div>

	</div>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import STATE_forms from '@/store/STATE_forms.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			step_info_class(int)
			{
				if (int < this.step) {
					return 'completed';
				}
				else if (int === this.step) {
					return 'current';
				}
				else {
					return '';
				}
			},
			step_line_class(int)
			{
				if (int < this.step) {
					return 'completed';
				}
				else if (int === this.step) {
					return 'pusher';
				}
				else {
					return '';
				}
			},
			step_tab_class(int)
			{
				if (int < STATE_forms().get_tab_page().current) {
					return 'completed';
				}
				else if (int === STATE_forms().get_tab_page().current) {
					return 'current';
				}
				else {
					return '';
				}
			},
			return_to_the_step(int)
			{
				// allow returning only for past steps
				if (int >= this.step) {
					return;
				}
				// in step5 session is confirmed and it will create new one
				if (this.step >= 5) {
					STATE_modals().open_it('Cleaning_session');
					return;
				}
				// most the time return backwards
				STATE_core().change_step(int);
			},
			tab_not_empty(tab)
			{
				let active_tab = Functions.active_tab(tab);
				return ! (active_tab === undefined/*missing tab*/ || Object.keys(active_tab).length === 0/*all hidden*/);
			},
			switch_tab(index)
			{
				if (!this.allow_tab_switch) {
					return;
				}
				STATE_forms().set_tab_page(index);
			},
		},
		computed: {
			step()
			{
				return STATE_core().current_step();
			},
			tab_count()
			{
				return STATE_forms().get_tab_page().keys;
			},
			allow_tab_switch()
			{
				// only our testing agent is allowed to switch tabs in progress_bar
				return STATE_params().capable_agent();
			},
			not_shown_in_technical_steps()
			{
				return this.step < 10;
			},
		},
		watch: {
		},
	}
</script>
