import STATE_core from '@/store/STATE_core.js';
import STATE_modals from '@/store/STATE_modals.js';
import STATE_error from '@/store/STATE_error.js';

/**
 * Request_service
*/
class Request_service
{
	params = {/*
		url: '', // required
		method: '', // required
		action_name: '', // required
		counter: 0, // never set  COUNTER REMOVED
		body: null, // optional
		timeout: 29, // optional
		loop_max: 4, // optional COUNTER REMOVED
		content_type: 'application/json', // optional
	*/};
	headers = {};
	auth_fails = 0;
	auth_max = 6;


	/**
	 * prepare_params
	*/
	prepare_params(params) // this current class iteration
	{
		// store for later
		this.params = params;

		// getDefaultParams can be removed now
		// increase counter, or set first
		/* COUNTER REMOVED
		if (this.params.counter === undefined) {
			this.params.counter = 1;
		} else {
			this.params.counter++;
		}*/

		// default timeout is 29 seconds, some uses 10
		if (this.params.timeout === undefined) {
			this.params.timeout = 29;
		}

		// default loop_max is 4, but every request can set its own
		/* COUNTER REMOVED
		if (this.params.loop_max === undefined) {
			this.params.loop_max = 4;
		}*/

		/* THIS WAS REMOVED AND 'Content-Type' IS FILLED STRAIGHT TO ALLOW IT TO BE EMPTY (FILLED BY BROWSER) WHEN null
		if (this.params.content_type === undefined) {
			this.params.content_type = 'application/x-www-form-urlencoded';
		}
		*/

		// request header options
		this.headers = {
			method: this.params.method,
			mode: 'cors',
			cache: 'no-cache',
			credentials: 'omit',
			headers: {
				'x-user-scope': STATE_core().give_api_scope(),
				'Authorization': STATE_core().give_bearer(),
				// 'Content-Type': this.params.content_type,
				'Accept': 'application/json',
			},
			// body: null,
		}
		if (this.params.method === 'POST' && this.params.body /*undefined|null|string*/) {
			this.headers.body = this.params.body;
		}
		if (this.params.content_type === undefined) {
			this.headers.headers['Content-Type'] = 'application/x-www-form-urlencoded';
		} else if (this.params.content_type !== null) {
			this.headers.headers['Content-Type'] = this.params.content_type;
		} // else we dont fill null, it is filled by browser

		// allow chaining
		return this;
	}


	/**
	 * unauthorized
	 * waits for async request asking its backend for access_token, saves it into store
	 * returns success boolean, whether to repeat request which had 401
	*/
	async unauthorized()
	{
		// each type has different way to get access_token
		let backend_type = STATE_core().get_backend_type();

		// php wrapper
		if (backend_type === 'php' || backend_type === 'np_wp')
		{
			let auth = await window.osm_php_backend.auth_service();
			STATE_core().save_bearer(auth);
			return true;
		}

		// lukas local development
		else if (backend_type === 'lukas_local')
		{
			let url = 'http://wrapper.local/authorize_frontend/?endpoint_type=' + STATE_core().get_endpoint_type();
			return fetch(url).then(
				(response) => {
					return response.json();
				}
			).then(
				(result) => {
					STATE_core().save_bearer(result);
					console.log('NEW BEARER:'); console.log(result);
					return true;
				}
			);
		}

		// vue without backend
		else if (backend_type === 'vue')
		{
			console.log('VUE does not have 401 auth service, fill access_token into env.js');
			return false;
		}

		// integrated in myport frontend
		else if (backend_type === 'myport')
		{
			return STATE_core().myport_access_token_promise().then(
				(auth/*brearer string | false | 503*/) => {
					if (auth === false) {
						return false;
					}
					STATE_core().save_bearer(auth);
					return true;
				}
			);
		}

		// fallback error
		else
		{
			console.log('backend_type ' + backend_type + ' has no access to new authorization');
			return false;
		}
	}


	/**
	 * handle_response
	*/
	async handle_response()
	{
		// fetch runs here
		let response = await this.call_the_api_request();

		/* 200
		**************************************************************************/
		if (response.status === 200)
		{
			// on success reset auth attempts
			this.auth_fails = 0;
			return response;
		}
		/* 401
		**************************************************************************/
		else if (response.status === 401)
		{
			// allow only certain auth request attempts, so wont get stuck on repeat
			this.auth_fails++;
			if (this.auth_fails > this.auth_max) {
				console.log('Authorization reched maximum attempts. Try to refresh the app or report to admin.');
				return {};
			}
			// 401 means request was unauthorized, get new token and repeat the request with old params
			let success = await this.unauthorized();
			// exit if calling access_token failed
			if (!success) { return null; }
			// repeat request with new access_token
			return await this.prepare_params(this.params).handle_response();
		}
		/* 423
		**************************************************************************/
		else if (response.status === 423)
		{
			// this error is thrown when agent code does not have valid clearance for current product group
			// agent code is set manually or from URL parameter &kod=(int)
			STATE_modals().open_it('Clearance');
			return response;
		}
		/* 503
		**************************************************************************/
		else if (response.status === 503)
		{
			STATE_core().change_step(503);
			return response;
		}
		/* 408
		**************************************************************************/
		else if (response.status === 408)
		{
			console.log('408 in request ' + this.params.action_name);
			// few apis handles timeout in their own way, those wont display info modal
			if (['calculate_step2', 'calculate_step4', 'confirm'].indexOf(this.params.action_name) === -1) {
				STATE_modals().open_it('Server_timeout');
			}
			// prevent promise errors
			return response;
		}
		/* action specific 422 423 425 409 500 504
		**************************************************************************/
		else if (
			// happens in few rare products in step2 calculate, just hide errored product
			(response.status === 500 && this.params.action_name === 'calculate_step2')
			||
			// confirm 422 error (step4) wont redirect to error page, but display error modal instead, rest will redirect to error page
			(response.status === 422 && this.params.action_name === 'confirm')
			||
			// getsessioninfo used on error page can not send back to error page because it will cause loop
			// this solution is not final, and could contain unforseen bugs
			(response.status === 422 && this.params.action_name === 'getsessioninfo')
			||
			// in ico-info and car-data-search requests we throw away any received error, no further action is needed, just display simple text message under field
			(this.params.action_name === 'ico-info' || this.params.action_name === 'car-data-search' )
			||
			// session-remap could return 422 as well, process it without redirecting to error page
			(response.status === 422 && this.params.action_name === 'session-remap')
			||
			// in id-scan/get-scan-groups call, errors return 422 with message
			(response.status === 422 && this.params.action_name === 'id-scan/get-scan-groups')
			||
			// invalid codes are returned with 422, already signed sessions returned with 409
			(( response.status === 422 || response.status === 409 ) && this.params.action_name === 'signature-code-check')
			||
			// signature-code-send can obviously retur 422 and 409 as well
			(( response.status === 422 || response.status === 409 ) && this.params.action_name === 'signature-code-send')
			||
			// in id-scan/get-scan-groups call, 500 is returned when no image was submited before
			(response.status === 500 && this.params.action_name === 'id-scan/get-scan-groups')
			||
			// id-scan/get-scan-groups is returned with 425 Too early in case it is processing OP images and not yet done
			(response.status === 425 && this.params.action_name === 'id-scan/get-scan-groups')
			||
			// getgroupform could bring 422 in case agent does not exist
			(response.status === 422 && this.params.action_name === 'getgroupform')
			||
			// if payment was already paid generating gateways throw error, dont redirect to error page, just for debugging purpose
			(response.status === 422 && this.params.action_name === 'payment-create')
			// ||
			// server timed out
			// response.status === 504
		) {
			return response;
		}
		/* all other shows error page
		**************************************************************************/
		else {
			let date = new Date().toLocaleString();
			let session = STATE_core().get_session_token();
			let step = STATE_core().current_step();
			STATE_error().set_last_request(this.headers, this.params, date, session, step, response);
			STATE_core().change_step(404);
			STATE_modals().close_them_all(); // end loading
			return response; // just to prevent error since promises are expecting response object
		}
	}


	/**
	 * call_the_api_request
	*/
	call_the_api_request()
	{
		return new Promise(
			(resolve, reject) => {

				/* COUNTER REMOVED
				if (this.params.counter > this.params.loop_max) {
					return resolve({status: 508, statusText: 'Loop Detected'});
				}*/

				// fake timeout, usually after 29 seconds
				setTimeout(
					function() {
						return resolve({status: 408, statusText: 'Request Timeout'});
					},
					this.params.timeout * 1000
				);

				// we dont return just result, but all response with procesed result
				let storage = {};

				// fetch it finally
				fetch(this.params.url, this.headers)
				.then(
					(response) => {
						storage = response;
						return response.json();
					}
				).then(
					(response) => {
						storage.result = response;
						resolve(storage);
					}
				);

			}
		);
	}
}


// without new keyword, each request has own iteration
export default Request_service;
