import STATE_params from '@/store/STATE_params.js';
import Functions from '@/logic/Functions.js';

/**
 * Show_hide
*/
class Show_hide
{
	id = '';
	type = '';
	hide_received = '';
	hide_parsed = '';
	field_names = [];
	hide_replaced = '';
	hide_evaluated = '';

	/**
	 * visibility_boolean
	*/
	visibility_boolean(/*obj*/ input)
	{
		// constructor
		this.prepare_input(input);

		// always show without hide-condition
		if (this.hide_received === '') {
			return true;
		}

		// actions here
		this.parse_condition();
		this.get_field_names();
		this.replace_conditions_with_input_values();
		this.evaluate_condition();
		this.purge_value();

		// visible if not hidden
		return ! this.hide_evaluated;
	}

	/**
	 * prepare_input
	*/
	prepare_input(input)
	{
		this.id = Object.keys(input)[0];

		// block
		if (this.id === '_block') {

			this.hide_received = input[this.id]['block_hide_condition'];
			this.type = {};

			// save each _block input type
			for (let order in input._block.elements) { if (!input._block.elements.hasOwnProperty(order)) { continue; }

				if (Functions.contains_only_digits(order))
				{
					let id = Functions.first_key(input._block.elements[order]);
					this.type[id] = input._block.elements[order][id].type;
				}
				else
				{
					// backwards compatibility, remove after _block order goes to production BE
					let id = order;
					this.type[id] = input._block.elements[order].type;
				}

			}

		}

		// single
		else {
			this.hide_received = input[this.id]['data-hide-condition'];
			this.type = input[this.id]['type'];
		}
	}

	/**
	 * parse_condition
	*/
	parse_condition()
	{
		// parse backoffice format into JS comparable conditions
		let string = this.hide_received;

		let space	= new RegExp('[ ]', 'g');
		let eq		= new RegExp('[=][=]', 'g');
		let lt		= new RegExp('[&][l][t][;]', 'g');
		let gt		= new RegExp('[&][g][t][;]', 'g');
		let less	= new RegExp('[<]', 'g');
		let more	= new RegExp('[>]', 'g');
		let n		= new RegExp('[!]', 'g');
		let plus	= new RegExp('\\+', 'g');
		let minus	= new RegExp('\\-', 'g');
		let les_eq	= new RegExp('< =', 'g');
		let mor_eq	= new RegExp('> =', 'g');
		let not_eq	= new RegExp('!=', 'g');
		let qot_2d	= new RegExp('&quot;&quot;', 'g');
		let qot_2s	= new RegExp('&#039;&#039;', 'g');
		let qot_1d	= new RegExp('&quot;', 'g');
		let qot_1s	= new RegExp('&#039;', 'g');
		let amp		= new RegExp('[&][a][m][p][;]', 'g');
		let and		= new RegExp('[&]', 'g');
		let or		= new RegExp('[|]', 'g');

		string = string.replace(space, '');

		string = string.replace(eq, ' == '); // pridaj medzeru kvôli parsovaniu neskôr
		string = string.replace(lt, '<'); // nahrad kod znamienkom a pridaj medzeru
		string = string.replace(gt, '>'); // nahrad kod znamienkom a pridaj medzeru

		string = string.replace(less, ' < '); // nahrad kod znamienkom a pridaj medzeru
		string = string.replace(more, ' > '); // nahrad kod znamienkom a pridaj medzeru
		string = string.replace(n, ' !'); // pridaj medzeru

		string = string.replace(plus, ' + ');
		string = string.replace(minus, ' - ');
		string = string.replace(les_eq, '<= ');
		string = string.replace(mor_eq, '>= ');
		string = string.replace(not_eq, '!= ');

		string = string.replace(qot_2d, '-1'); // oprav uvodzovky double
		string = string.replace(qot_2s, '-1'); // oprav uvodzovky single

		string = string.replace(qot_1d, "'"); // zabezpec porovnavanie stringu
		string = string.replace(qot_1s, "'"); // zabezpec porovnavanie stringu

		string = string.replace(amp, '&');
		string = string.replace(and, ' && '); // oprav AND
		string = string.replace(or, ' || '); // oprav OR

		this.hide_parsed = string;
	}

	/**
	 * get_field_names
	*/
	get_field_names()
	{
		// extract field IDs/names from condition string
		let string = this.hide_parsed;
		let names_array = [];

		// if condition has brackets, explode it by them as well
		string = string.split(')').join('&');
		string = string.split('(').join('&');
		let splitted = string.split(/[\s&|()=<>!]+/);

		// remove empty strings from array
		var i, len = splitted.length;
		for (i = len-1; i >= 0; i--) {
			if (splitted[i] === '') {
				splitted.splice(i, 1);
			}
		}

		// check for dolar in condition
		if (string.indexOf('$') > -1) {

			// if string is having dolar, get all strings starting with dolar
			var i, len = splitted.length;
			for (i = 0; i < len; i++) {
				if (splitted[i].indexOf('$') === 0) {
					names_array.push(splitted[i]);
				}
			}

		} else {

			// if not, every odd (not even) item in array is the name of field
			var i, len = splitted.length;
			for (i = 0; i < len; i++) {
				if (i % 2 === 0) {
					names_array.push(splitted[i]);
				}
			}

		}

		// contains duplicates
		this.field_names = names_array;
	}

	/**
	 * replace_conditions_with_input_values
	*/
	replace_conditions_with_input_values()
	{
		let string = this.hide_parsed;

		let i, len = this.field_names.length;
		for (i = 0; i < len; i++) {

			let id = this.field_names[i];
			let value = STATE_params().get_user_input(id.replace('$', ''));
			if (value === undefined || value === '') {
				value = -1; // '' is replaced by -1 in condition string
			} else if (Functions.contains_some_letters(value)) {
				value = '"' + value + '"';
			}
			string = string.replace(id, value);

		}

		this.hide_replaced = string;
	}

	/**
	 * evaluate_condition
	*/
	evaluate_condition()
	{
		let condotion_string = this.hide_replaced;

		try {

			// accepts string, returns boolean
			this.hide_evaluated = eval(condotion_string);

		} catch (error) {

			// report errors
			if (error instanceof SyntaxError) {
				console.log('syntax error', error.message);
				console.log(this.hide_received, ' >>>>>> ', condotion_string);
				this.hide_evaluated = false;
			} else {
				console.log('probably parsing error, check for missing $', error);
				console.log(this.hide_received, ' >>>>>> ', condotion_string);
				this.hide_evaluated = false;
			}

		}
	}

	/**
	 * purge_value
	*/
	purge_value()
	{
		// leave shown inputs
		if (this.hide_evaluated === false) {
			return;
		}
		// block
		if (this.id === '_block') {
			for (let key in this.type) { if (!this.type.hasOwnProperty(key)) { continue; }
				this.purge_helper(this.type[key], key);
			}
			return;
		}
		// single
		else {
			this.purge_helper(this.type, this.id);
		}
	}


	/**
	 * purge_helper
	*/
	purge_helper(type, id)
	{
		// radios and checkboxes keep value when hidden
		if (type === 'radio' || type === 'checkbox' || type === 'checkbox_icon' || (type === 'select' && id !== 'havarijne_auto_vybava')) {
			return;
		}
		// remove value if hiding input
		STATE_params().delete_form_value(id);
	}
}

export default new Show_hide;
