<template>
	<h4>Platba prevodom na účet</h4>
	IBAN: <strong>{{ payment_info.iban }}</strong><br>
	SWIFT: <strong >{{payment_info.swift  }}</strong><br>
	Platba: <strong>{{ payment_info.amount }}</strong><br>
	Variabilný symbol: <strong>{{ payment_info.vs }}</strong><br>
	Konštantný symbol: <strong>{{ payment_info.ks }}</strong><br>
	<!-- we temporary skip duedate on clients request -->
	<!-- Splatnosť: <strong >{{ payment_info.duedate }}</strong><br> -->
</template>

<script>
	import Payment_requests from '@/logic/requests/Payment_requests.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
				payment_info: {
					// amount: '105.84',
					// duedate: '2022-12-24',
					// iban: 'SK8211000000002628043793',
					// ks: '3558',
					// ss: '',
					// swift: 'TATRSKBX',
					// vs: '2222000050',
				},
			}
		},
		created() {
			// get money transfer info
			Payment_requests.payment_info().then(
				(response) => {

					// fallback
					if (response.status !== 200) {
						return;
					}

					// success
					this.payment_info = response.result.paymentinfo;

				}
			);
		},
		methods: {
		},
		computed: {
		},
		watch: {
		},
	}
</script>
