<template>
	<Modal_body :close="false" title="Potvrdenie SMS kódom" class="modal-signature" >

		<!-- sending -->
		<div v-show="state === 'sending'" >
			<p>
				Odosielame SMS kód na vaše telefónne číslo
			</p>
			<Bouncer_loading />
		</div>

		<!-- not_sent -->
		<div v-show="state === 'not_sent'">
			<p>
				Odosielanie SMS správy zlyhalo.
			</p>
			<p>
				{{ message }}
			</p>
		</div>

		<!-- already_sent -->
		<div v-show="state === 'already_sent'" >
			<p>
				{{ message }}
			</p>
		</div>

		<!-- is_sent -->
		<div v-show="state === 'is_sent'" >
			<p>
				SMS správa s kódom bola vygenerovaná a mala by Vám byť doručená v krátkej dobe. <br>
				Zadajte SMS kód, ktorý ste obdržali. <br>
				Zadaním SMS kódu vyjadrujete súhlas s obsahom obdržanej predzmluvnej dokumentácie.
			</p>
			<input
				v-model="sms_code"
				@keydown="clear_error"
				@keydown.enter="code_validation"
				ref="sms_code"
				type="text"
				name="sms_code"
				class="form-control mb-2 mx-auto mx-sm-0"
			>
			<p v-show="invalid_format" >
				Bol zadaný nesprávny formát. SMS kód je 5 ciferné číslo.
			</p>
			<button
				@click="code_validation"
				class="btn btn-accent btn-right btn-lg mx-auto"
			>
				Skontrolovať SMS kód
			</button>
		</div>

		<!-- checking -->
		<div v-show="state === 'checking'" >
			<p>
				Kontrolujeme platnosť SMS kódu.
			</p>
			<Bouncer_loading />
		</div>

		<!-- failed -->
		<div v-show="state === 'failed'" >
			<p>
				{{ message }}
			</p>
			<button
				@click="ask_for_code"
				class="btn btn-primary btn-left btn-lg"
			>
				Opraviť
			</button>
		</div>

		<!-- success -->
		<div v-show="state === 'success'" >
			<p>
				SMS kód je platný. <br>
				Zmluva bola schválená. <br>
				Presmerovávame Vás na záverečnú časť.
			</p>
			<Bouncer_loading />
		</div>

		<!-- no_confirm -->
		<div v-show="state === 'no_confirm'" >
			<p>
				SMS potvrdenie predzmluvnej dokumentácie <br> prebehlo v poriadku. <br>
				Prosím zatvorte túto stránku a počkajte na daľšie instrukcie k uzatvoreniu poistnej zmluvy od vášho sprostredkovateľa.
			</p>
		</div>

	</Modal_body>
</template>

<script>
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import Confirm_requests from '@/logic/requests/Confirm_requests.js';
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import Signatures from '@/logic/requests/Signatures.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_core from '@/store/STATE_core.js';
	import Functions from '@/logic/Functions.js';
	import Session_data from '@/logic/requests/Session_data.js';

	export default {
		props: {
		},
		components: {
			Bouncer_loading,
			Modal_body,
		},
		emits: [],
		data() {
			return {
				state: 'sending', // sending, not_sent, already_sent, is_sent, checking, failed, success, no_confirm
				message: '',
				sms_code: '',
				invalid_format: false,
			}
		},
		created() {
			this.ask_for_code();
			// this.state = 'is_sent'; // debug
		},
		methods: {
			ask_for_code()
			{
				Signatures.code_send().then(
					(response) => {
						if (response.status === 422)
						{
							// error
							this.state = 'not_sent';
							this.message = response.result.message;
						}
						else if (response.status === 409)
						{
							// alredy singed code
							this.state = 'already_sent';
							this.message = response.result.message;
							this.confirm_or_close();
						}
						else if (response.status === 200 && response.result.status !== 'fail')
						{
							// success
							this.state = 'is_sent';
							this.$nextTick(
								() => {
									this.$refs.sms_code.focus();
								}
							);
						}
						else
						{
							// error
							this.state = 'not_sent';
							this.message = JSON.stringify(response.result);
						}
					}
				);
			},
			code_validation()
			{
				// input FE validation
				if (
					this.sms_code.length !== 5 ||
					Functions.contains_only_digits(this.sms_code) === false
				) {
					this.invalid_format = true;
					return;
				}
				this.state = 'checking';

				// BE validation
				let checkboxes = STATE_modals().retrieve_modal_data().checkboxes;
				Signatures.code_check(checkboxes, this.sms_code).then(
					(response) => {
						if (response.status === 422)
						{
							// error
							this.state = 'failed';
							this.message = response.result.message;
						}
						else if (response.status === 409)
						{
							// alredy singed code
							this.state = 'already_sent';
							this.message = response.result.message;
							this.confirm_or_close();
						}
						else
						{
							// success
							this.state = 'success';
							this.confirm_or_close();
						}
					}
				);
			},
			confirm_or_close()
			{
				// duplicit confirm from Step4_proposal, just display close info and change signature_state
				let mode = STATE_modals().retrieve_modal_data().mode;
				if (mode === 'provider_client') {
					this.state = 'no_confirm';
					// update signature state
					Session_data.set_data({ signature_state: 'agent_autoconfirm', });
				}
				// common confirm
				else {
					// checkboxes are already sent in this case
					let checkboxes = {};
					Confirm_requests.confirm_session(checkboxes).then(
						(response) => {

							// close loading
							STATE_modals().close_them_all();

							// dunno how often this happens, just a fallback
							if (response.status !== 200) {
								console.log('confirm error', response);
								return;
							}

							// success
							if (response.result?.response === 'success'/*confirm*/ || response.result?.state === 100/*getsessioninfo*/) {
								STATE_core().change_step(5);
								return;
							}

							// baaad data
							console.log('confirm simple error');
							STATE_modals().store_modal_data(response.result);
							STATE_modals().open_it('Confirm_simple_error');
						}
					);
				}
			},
			clear_error()
			{
				this.invalid_format = false;
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
