import STATE_core from '@/store/STATE_core.js';
import Cookies from '@/logic/Cookies.js';

/**
 * Cookie_manager
*/
class Cookie_manager
{
	/**
	 * init_manager
	*/
	init_manager()
	{
		if (this.exit_manager()) {
			return;
		}

		// show group manager if group wasnt picked
		if (STATE_core().product_group_id() === -1) {
			STATE_core().change_step(111);
			return;
		}

		// retrieve session from cookies if it exists
		if (STATE_core().get_session_token() === '') {
			let cookie = Cookies.get_cookie(this.c_name('session'));
			if (cookie) {
				STATE_core().set_session_token(cookie, false);
			}
		}

		// retrieve step from cookies if it exists, default to 1
		if (STATE_core().current_step() === -1) {
			let cookie = Cookies.get_cookie(this.c_name('step'));
			if (cookie) {
				STATE_core().change_step(parseInt(cookie, 10));
			} else {
				STATE_core().change_step(1);
			}
		}
	}


	/**
	 * update_session
	*/
	update_session()
	{
		if (this.exit_manager()) {
			return;
		}
		Cookies.set_cookie(this.c_name('session'), STATE_core().get_session_token(), 14);
		Cookies.set_cookie(this.c_name('expire'), null, 14);
	}


	/**
	 * update_step
	*/
	update_step()
	{
		if (this.exit_manager()) {
			return;
		}
		Cookies.set_cookie(this.c_name('step'), STATE_core().current_step(), 14);
	}


	/**
	 * exit_manager
	*/
	exit_manager()
	{
		// do nothing if manager is off
		return !STATE_core().is_cookie_manager_on() || STATE_core().get_run_mode() !== 'form_app';
	}


	/**
	 * c_name
	*/
	c_name(type)
	{
		if (type !== 'session' && type !== 'step' && type !== 'expire') {
			console.log('Error: wrong cookie type.');
			return;
		}
		// f.e.: osm_2_session, osm_2_step, ...
		return 'osm_cookie_' + STATE_core().get_endpoint_type() + '_' + STATE_core().product_group_id() + '_' + type;
	}
}

export default new Cookie_manager;
