<template>
	<Modal_body :close="false" title="Chyba pri potvrdení" class="modal-error" >

		<p>
			Nebolo možné vygenerovať návrh zmluvy z nasledovného dôvodu:
		</p>

		<p>
			<span v-for="(value, key) in errors" >
				{{ key }} : {{ value }} <br>
			</span>
		</p>

		<button class="btn btn-primary btn-left mt-2 mb-4" @click="go_back">
			Vrátiť sa na predchádzajúci krok
		</button>

		<p>
			token: {{ token }}
			<br>
			time: {{ time }}
		</p>

		<p>
			Pre bližšie info o ďalšom postupe nás prosím kontaktuje: <br>
			<a :href="'mailto:' + mail">{{ mail }}</a>
			<br>
			<span v-if="phone">{{ phone }}</span>
		</p>

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_core from '@/store/STATE_core.js';
	import Constants from '@/logic/Constants.js';
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
		},
		components: {
			Modal_body,
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			go_back()
			{
				STATE_core().change_step(3);
			},
		},
		computed: {
			errors()
			{
				return STATE_modals().retrieve_modal_data()?.data?.errors;
			},
			token()
			{
				 return STATE_core().get_session_token();
			},
			time()
			{
				return new Date().toLocaleString();
			},
			mail()
			{
				return Constants.email_address();
			},
			phone()
			{
				if (STATE_params().direct_user_no_agent()) {
					return null;
				}
				return Constants.phone_number(true);
			},
		},
		watch: {
		},
	}
</script>
