<template>

	<!-- check request and fill data -->
	<div class="text-center">
		<button
			@click="send_scan_pages"
			:disabled="pages_not_ready"
			class="btn btn-primary"
		>
			Overiť a vyplniť údaje
		</button>

		<!-- check loading -->
		<div
			v-show="loading"
		>
			<Bouncer_loading size="small" />
		</div>
	</div>

</template>

<script>
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import Credentials_requests from '@/logic/requests/Credentials_requests.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';
	import STATE_core from '@/store/STATE_core.js';

	export default {
		props: {
			debug: Boolean,
			side_payloads: Object,
		},
		components: {
			Bouncer_loading,
		},
		emits: ['scan_feedback'],
		data() {
			return {
				loading: false,
				check_repeated: 0, // if scan check was too early, repeat it
				max_checks: 10, // but "timeout" after few tries
				check_delay: 2000, // 2s
			}
		},
		created() {
		},
		methods: {
			send_scan_pages()
			{
				if (this.pages_not_ready) {
					return;
				}

				this.loading = true;

				if (this.debug) {
					this.$emit('scan_feedback', 'sending pages');
				}

				Promise.resolve().then(
					() => {

						// front page
						return Credentials_requests.add_page(this.side_payloads['1']);

					}
				).then(
					(response) => {

						// error, should not happen
						if (response.status !== 200) {
							this.$emit('scan_feedback', 'Nahrávanie strany ' + 1 + ' zlyhalo.');
							console.log(response);
							return false;
						}

						// report, success
						if (this.debug) {
							this.$emit('scan_feedback', 'page ' + 1 + ' sent');
						}
						return true;

					}
				).then(
					async (success) => {

						// exit if front failed
						if (success !== true) {
							return success;
						}

						// wait 1 second, send-page api had performance problems if pages was sent too fast
						// in future we could probably send both pages together, or with quick succession
						let wait = await new Promise(
							(resolve, reject) => {
								setTimeout(
									() => { resolve(); },
									1000 // 1s
								);
							}
						);

						// back page
						return Credentials_requests.add_page(this.side_payloads['2']);

					}
				).then(
					(response) => {

						// error, should not happen
						if (response.status !== 200) {
							this.$emit('scan_feedback', 'Nahrávanie strany ' + 2 + ' zlyhalo.');
							console.log(response);
							return false;
						}

						// report, success
						if (this.debug) {
							this.$emit('scan_feedback', 'page ' + 2 + ' sent');
						}
						return true;

					}
				).then(
					(success) => {

						// dont check if any upload failed
						if (success !== true) {
							return;
						}

						// pages sent, check and ask for data
						this.credentials_check();

					}
				);
			},
			credentials_check()
			{
				if (this.debug) {
					this.$emit('scan_feedback', 'asking backend for scan check');
				}

				Credentials_requests.scan_groups().then(
					(response) => {

						// error 422
						if (response.status === 422) {
							this.loading = false;
							this.$emit('scan_feedback', '422 Unprocessable Entity, message: ');
							this.$emit('scan_feedback', response.result.message);
						}
						// error 500
						else if (response.status === 500) {
							this.loading = false;
							this.$emit('scan_feedback', '500 Internal Server Error, message: ');
							this.$emit('scan_feedback', response.result.message);
						}

						// too early, repeat request every few seconds, should take max 10 seconds
						else if (response.status === 425) {
							if (this.check_repeated === 0) {
								this.$emit('scan_feedback', 'Prebieha spracovanie.');
							}
							// count every repeat
							this.check_repeated++;
							// wait few seconds
							setTimeout(
								() => {
									// repeat few times
									if (this.check_repeated < this.max_checks) {
										if (this.debug) {
											this.$emit('scan_feedback', 'repeat check no. ' + this.check_repeated);
										}
										this.credentials_check();
									}
									// reached maximum
									else {
										this.$emit('scan_feedback', 'Overovanie dosiahlo maximálny počet pokusov.');
										this.loading = false;
									}
								},
								this.check_delay
							);
						}

						// success
						else {
							this.data = response.result;
							this.$emit('scan_feedback', 'Overovanie úspešné.');
							if (this.debug) {
								this.$emit('scan_feedback', JSON.stringify(this.data));
							}
							this.fill_data();
							this.store_docs_into_session();
							this.loading = false;
						}

					}
				);
			},
			fill_data()
			{
				if (this.debug) {
					this.$emit('scan_feedback', 'filling received data into form');
				}

				// save into session data
				STATE_params().set_multiple_form_values(this.data);

				// auto close modal if not in debug
				if (this.debug) {
					return;
				}
				setTimeout(
					() => { STATE_modals().close_them_all() },
					3000 // 3s
				);
			},
			store_docs_into_session()
			{
				if (STATE_core().product_group_id() !== 6/*sporenie*/) {
					return;
				}
				if (this.debug) {
					this.$emit('scan_feedback', 'start scan documents request');
				}
				// give backend info to create documents from uploaded files, only for group 6
				Credentials_requests.scan_group_documents().then(
					(response) => {

						// we dont need to process response
						if (!this.debug) {
							return;
						}
						if (response.status === 200) {
							this.$emit('scan_feedback', 'scan documents successful');
						} else {
							this.$emit('scan_feedback', 'scan documents failed');
						}

					}
				);
			},
		},
		computed: {
			pages_not_ready()
			{
				return !this.side_payloads['1'] || !this.side_payloads['2'] || this.loading;
			},
		},
		watch: {
		},
	}
</script>
