<template>
	<section class="session_cookie_manager">
		<table class="table table-striped table-hover table-borderless align-middle table-sm">
			<thead>
				<tr>
					<th scope="col">ID</th>
					<th scope="col">name</th>
					<th scope="col">step</th>
					<th scope="col">session</th>
					<th scope="col">expire</th>
					<th scope="col">open</th>
					<th scope="col">delete</th>
				</tr>
			</thead>
			<tbody>
				<tr
					v-for="(group, id) in all_data"
				>
					<th scope="row">{{ id }}</th>
					<td>{{ group.name }}</td>
					<td>{{ group.step }}</td>
					<td>{{ group.session }}</td>
					<td>{{ group.expire }}</td>
					<td>
						<button
							@click="open_this_group(id)"
							class="btn btn-sm btn-primary"
						>
							open
						</button>
					</td>
					<td>
						<button
							@click="clear_this_group(id)"
							class="btn btn-sm btn-secondary"
						>
							clear
						</button>
					</td>
				</tr>
			</tbody>
		</table>
	</section>
</template>

<script>
	import Constants from '@/logic/Constants.js';
	import Cookies from '@/logic/Cookies.js';
	import STATE_core from '@/store/STATE_core.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
				all_data: {
					// 1: {
					// 	name: '',
					// 	step: '',
					// 	token: '',
					// 	expiration: '',
					// },
					// ...
				},
			}
		},
		created() {
			let product_groups = Constants.group_names();
			for (let id in product_groups) { if (!product_groups.hasOwnProperty(id)) { continue; }

				let step = Cookies.get_cookie(this.c_name(id, 'step')) || 1;
				let session = Cookies.get_cookie(this.c_name(id, 'session')) || '';

				let expire = Cookies.get_cookie(this.c_name(id, 'expire'));
				if (!expire) {
					expire = '';
				} else {
					expire = new Date(expire);
					expire = expire.toLocaleString('sk-SK');
				}

				this.all_data[id] = {
					name: product_groups[id],
					step: step,
					session: session,
					expire: expire,
				};

			}
		},
		methods: {
			open_this_group(id)
			{
				STATE_core().set_session_token(this.all_data[id].session, false);
				STATE_core().change_product_group_id(id);
				STATE_core().change_step(parseInt(this.all_data[id].step, 10));
			},
			clear_this_group(id)
			{
				Cookies.delete_cookie(this.c_name(id, 'step'));
				Cookies.delete_cookie(this.c_name(id, 'session'));
				Cookies.delete_cookie(this.c_name(id, 'expire'));
				this.all_data[id].step = 1;
				this.all_data[id].session = '';
				this.all_data[id].expire = '';
			},
			c_name(id, type)
			{
				if (type !== 'session' && type !== 'step' && type !== 'expire') {
					console.log('Error: wrong cookie type.');
					return '';
				}
				return 'osm_cookie_' + STATE_core().get_endpoint_type() + '_' + id + '_' + type;
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>

<style lang="scss">
	.session_cookie_manager {
		button {
			padding: 4px 12px;
		}
	}
</style>
