/**
 * Cookies
*/
class Cookies
{
	/**
	 * set_cookie
	*/
	set_cookie(name/*STR*/, value/*STR*/, days_or_midnight/*INT >= 0*/)
	{
		// define date
		let date = new Date();
		let expire = '';

		// midnight
		if (days_or_midnight === 0) {
			let now = new Date();
			let night = new Date();
			night.setHours(26, 0, 33); // not exactly midnight, but 02:00:33
			expire = night.valueOf() - now.valueOf();
		}
		// debug, expire in 5 seconds
		else if (days_or_midnight < 0) {
			expire = 5000; // 5s
		}
		// days
		else {
			expire = days_or_midnight * 24/*hours*/ * 60/*minutes*/ * 60/*seconds*/ * 1000/*miliseconds*/;
		}

		// set current date plus remaining time
		date.setTime(date.getTime() + expire);

		// expire uses expiration as value
		if (value === null) {
			value = date.toUTCString();
		}

		// save cookie in specific format
		document.cookie = name + '=' + (value || '') + '; expires=' + date.toUTCString() + '; path=/;' + this.same_site_secure();
	}


	/**
	 * get_cookie
	*/
	get_cookie(name)
	{
		// cookies are string delimited by '; '
		// '{name}={value}; {name}={value}; ...'
		let all_cookies = document.cookie.split('; ');

		// loop them all in array
		let i, len = all_cookies.length;
		for (i = 0; i < len; i++) {

			// skip if it doesnt start with name
			if (all_cookies[i].indexOf(name) !== 0) {
				continue;
			}

			// remove '{name}=', return only value
			return all_cookies[i].substring(name.length + 1);

		}
	}


	/**
	 * delete_cookie
	*/
	delete_cookie(name)
	{
		// rewrite cookie value to empty and sets expiration to past
		document.cookie = name + '=; expires=Thu, 01 Jan 1970 00:00:01 GMT; path=/;' + this.same_site_secure();
	}


	/**
	 * same_site_secure
	*/
	same_site_secure()
	{
		// http: doesnt use SameSite parameter
		return window.location.protocol === 'https:' ? ' SameSite=None; Secure;' : '';
	}
}

export default new Cookies;
