<template>
	<div
		:id="product_id"
		:class="show_loading_animation ? 'the_form_is_disabled' : ''"
		class="row product g-0"
		data-cy="product"
	>
		<!-- AREA product info -->
		<div class="col-lg-10 pr_area_info pb-2 pb-lg-1 position-relative">

			<!-- promo -->
			<div
				v-show="p_data.product.promo === '1' && p_data.product.promo === '2'"
				class="pr_ribbon"
			>
				<span>NÁŠ TIP</span>
			</div>

			<!-- top part -->
			<div class="pr_top">
				<div class="row g-0">

					<!-- BOX logo -->
					<div class="col-lg-2">
						<div class="pr_box_logo text-center mb-2 mb-lg-0">
							<img :src="p_data.logo">
						</div>
					</div>

					<div class="col-lg-4">
						<!-- BOX title, rating etc. box -->
						<div class="pr_box_name text-center text-lg-start">

							<!-- title, id only for info -->
							<h4 :id="'#' + product_id" >
								{{ product_name }}
							</h4>

							<!-- stars -->
							<div
								v-if="not_zeteo_product"
								v-show="p_data.product.stars > 0"
								class="pr_rating"
							>
								<span
									v-for="index in 5"
									:class="[index > p_data.product.stars ? 'i_star_empty' : 'i_star']"
								></span>
							</div>

							<div class="pr_select" v-show="p_data.spoluucast_krytie || p_data.product.spoluucast_text">
								<!-- spoluucast / krytie - dropdown -->
								<Select
									v-for="(field_data, field_id) in p_data.spoluucast_krytie"
									:field_id="field_id"
									:field_data="field_data"
									emit_name="recalculate_product"
									@recalculate_product="recalculate_product"
								/>
								<!-- spoluucast plaintext -->
								<p
									v-show="!p_data.spoluucast_krytie"
								>
									{{ p_data.product.spoluucast_text }}
								</p>
							</div>

							<!-- detail modal trigger-->
							<div class="detail-compare text-nowrap">
								<span class="pr_detail">
									<button
										v-show="p_data.product.detail === '2'"
										@click="open_product_detail"
										class="btn-text i_anchor"
									>
										<span class="i_detail"></span>
										info o produkte
									</button>
									<!-- graph trigger -->
									<!-- <a href="#" class="detail-modal hidden" data-toggle="modal" data-target="detail" product-graph_button="">
										<span class="i_detail_modal"></span>GRAF
									</a> -->
								</span>
							</div>

						</div>
					</div>
					<hr class="pr_divider d-block d-lg-none">
					<!-- BOX quick overview -->
					<div class="col-lg-6">
						<div class="pr_box_overview">
							<ul>

								<!-- icons -->
								<li
									v-for="icon in p_data.icons"
									:class="icon.class"
								>
									<span
										v-html="icon.title"
										class="bold_move_to_side"
									></span>
									<div
										v-if="icon.tooltip"
										class="ttip ttip-top euro_move_to_side"
									>
									(€)
										<span
											v-html="icon.tooltip"
											class="ttip_text"
										></span>
									</div>
								</li>

							</ul>
						</div>
					</div>
				</div>
			</div>
			<hr class="pr_divider">

			<!-- bottom part -->
			<div class="pr_bottom">
				<div class="row g-0">
					<div class="col-lg-6">

						<!-- BOX benefits -->
						<div class="pr_box_included">
							<ul>
								<li
									v-for="benefit in p_data.benefits"
									:class="benefit.class"
								>
									{{ benefit.title }}
								</li>
							</ul>
						</div>

					</div>
					<hr class="pr_divider d-block d-lg-none">
					<div class="col-lg-6">
						<!-- BOX options / product form -->
						<div class="pr_box_options">
							<ul>

								<!-- product inputs -->
								<li
									v-for="(field_data, field_id) in p_data.inputs"
								>
									<Checkbox
										v-if="field_data.type === 'checkbox'"
										:field_id="field_id"
										:field_data="field_data"
										emit_name="recalculate_product"
										@recalculate_product="recalculate_product"
									/>
									<Select
										v-if="field_data.type === 'select' && field_data.type_settings?.select_type !== 'multiple'"
										:field_id="field_id"
										:field_data="field_data"
										emit_name="recalculate_product"
										@recalculate_product="recalculate_product"
									/>
									<Multiple
										v-if="field_data.type === 'select' && field_data.type_settings?.select_type === 'multiple'"
										:field_id="field_id"
										:field_data="field_data"
										emit_name="recalculate_product"
										@recalculate_product="recalculate_product"
									/>
									<Datepicker
										v-if="field_data.type === 'date'"
										:field_id="field_id"
										:field_data="field_data"
										emit_name="recalculate_product"
										@recalculate_product="recalculate_product"
									/>
								</li>

							</ul>
						</div>
					</div>
				</div>

				<!-- crossell -->
				<div
					v-if="p_data.product.crossell"
					class="pr_bundle"
				>
					<span class="bundle_img"></span>
					<p>
						{{ p_data.product.crossell }}
					</p>
					<!-- cta not in use -->
					<!-- <span class="bundle_cta"><a href="#">vypočítať cenu</a></span> -->
				</div>

			</div>
		</div>

		<!-- AREA product price & cta -->
		<div class="col-lg-2">
			<div class="pr_area_price d-flex flex-column flex-sm-row flex-lg-column text-center h-100">

				<!-- discount -->
				<div class="pr_discount">
					<span
						v-if="p_data.price.base > 0"
						title="Základná cena pred zľavami"
						class="before_discount"
					>
						{{ p_data.price.base }}€
					</span>
					<span
						v-if="p_data.price.discount > 0"
						title="Celková zľava"
						class="discount_tag"
					>
						{{ p_data.price.discount }}%
					</span>

				</div>

				<!-- price -->
				<div
					v-show="!show_loading_animation && p_data.price.for_period"
					@click="get_price_ws_details"
					class="pr_price"
					:class="p_data.product.limity ? 'price_has_details' : ''"
				>
					<span class="amount">{{ p_data.price.for_period }}</span>
					<span class="text-default">{{ p_data.price.currency }}</span>
					<span class="period">{{ p_data.price.period_label }}</span>
				</div>

				<!-- loading -->
				<div v-show="show_loading_animation" class="pr_price">
					<Bouncer_loading />
				</div>

				<!-- product select button -->
				<div
					v-show="!show_loading_animation"
					class="pr_cta"
				>

					<!-- error -->
					<span
						v-if="p_data.error.errors"
						class="btn btn-inactive w-100"
					>
						<span
							:class="p_data.error.alt_text ? 'viacinfo' : ''"
						>
							{{ p_data.error.alt_text ? 'VIAC INFO' : 'Nedá sa poistiť' }}
						</span>
						<div
							v-show="p_data.error.errors"
							class="ttip"
						>
							?
							<span
								v-html="p_data.error.errors"
								class="ttip_text"
							></span>
						</div>
					</span>

					<!-- redirect -->
					<button
						v-else-if="p_data.button.show === '3'"
						@click="redirect_to_product"
						class="btn btn-xlg btn-right btn-accent w-100"
					>
						{{ not_zeteo_product ? 'Vybrať' : 'Uzatvoriť' }}
					</button>

					<!-- info -->
					<span
						v-else-if="p_data.button.show === '2'"
						class="btn btn-inactive w-100"
					>
						<span
							:class="p_data.error.alt_text ? 'viacinfo' : ''"
						>
							{{ p_data.error.alt_text ? 'VIAC INFO' : 'Nedá sa poistiť' }}
						</span>
						<div
							v-show="p_data.error.info"
							class="ttip"
						>
							?
							<span
								v-html="p_data.error.info"
								class="ttip_text"
							></span>
						</div>
					</span>

					<!-- product select -->
					<button
						v-else-if="p_data.button.show === '1'"
						@click="select_this_product"
						class="btn btn-xlg btn-right btn-accent w-100"
					>
						{{ not_zeteo_product ? 'Vybrať' : 'Uzatvoriť' }}
					</button>

					<!-- warning -->
					<div
						v-if="p_data.button.show === '1' && p_data.product.warning"
						class="ttip pr_warning"
					>
						!
						<span class="ttip_text">
							{{ p_data.product.warning }}
						</span>
					</div>

				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import Product_requests from '@/logic/requests/Product_requests.js';
	import Product_helpers from '@/components/form_app/product/Product_helpers.js';
	import Checkbox from '@/components/forms/basic_inputs/Checkbox.vue';
	import Select from '@/components/forms/basic_inputs/Select.vue';
	import Multiple from '@/components/forms/advanced_inputs/Multiple.vue';
	import Datepicker from '@/components/forms/advanced_inputs/Datepicker.vue';
	import Functions from '@/logic/Functions.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import Session_data from '@/logic/requests/Session_data.js';
	import Get_form_html from '@/logic/requests/Get_form_html.js';

	export default {
		props: {
			product_id: String,
			product_name: String,
			response: Object,
			being_calculated: Array,
		},
		components: {
			Checkbox,
			Select,
			Multiple,
			Datepicker,
			Bouncer_loading,
		},
		emits: ['recalculate_product'],
		data() {
			return {
				loading: true,
				p_data: {
					logo: '',
					error: {
						errors: '',
						alt_text: false,
						info: '',
					},
					button: {
						show: '0',
						next_form: '',
						next_contract: '',
						next_redirect: '',
					},
					price: {
						for_period: '',
						currency: '',
						period_label: '',
						base: '',
						discount: '',
					},
					product: {
						promo: '',
						stars: 0,
						spoluucast_text: '',
						detail: '',
						detail_template: '',
						crossell: '',
						warning: '',
						limity: false,
					},
					icons: [
						// {
						// 	title: '',
						// 	tooltip: '',
						// 	order: 0,
						// 	class: '',
						// },
						// ...
					],
					benefits: [
						{
							title: '',
							class: '',
						},
					],
					inputs: {
						// kooperativa_krytie: {
						// 	type: 'select',
						// 	enum: {...},
						// 	type_settings: {...},
						// },
						// ...
					},
					spoluucast_krytie: {
						// havarijne_kooperativa_spoluucast: {
						// 	type: 'select',
						// 	enum: {...},
						// 	type_settings: {...},
						// },
					},
				},
			}
		},
		created() {
			this.asign_values();
		},
		watch: {
			response()
			{
				this.asign_values();
			},
		},
		methods: {
			asign_values()
			{
				// reset this.p_data to original state when recalculate happens
				if (typeof this.response !== 'object') {
					Object.assign(this.$data, this.$options.data());
					return;
				}

				// result inside product result
				let result = this.response.result[this.product_id].result;

				// frekvenciaNP
				let period = Product_helpers.find_period(this.response);

				// product data
				this.p_data = {
					logo: result?.head?.logo?.comment,
					period: period,
					error: {
						errors: Product_helpers.error_tooltip(result),
						alt_text: Product_helpers.check_alt_text(),
						info: Product_helpers.info_tooltip(result),
					},
					price: {
						for_period: Product_helpers.price_for_period(result, period),
						currency: result?.result?.currency?.result || '€',
						period_label: Product_helpers.label_for_period(result, period),
						base: result?.result?.base?.result,
						discount: result?.result?.discount?.result,
					},
					button: {
						show: result?.next?.next?.result, // '0' hidden, '1' show select product, '2' show info, '3' redirect elsewhere
						next_form: result?.next?.next_form?.result,
						next_contract: result?.next?.next_contract_html?.result,
						next_redirect: result?.next?.next_redirect?.result,
					},
					product: {
						promo: result?.product?.promo?.result,
						stars: parseInt(result?.product?.stars?.result, 10),
						spoluucast_text: result?.product?.spoluucast?.formula_name,
						detail: result?.product?.detail?.result,
						detail_template: result?.product?.detail?.comment,
						crossell: result?.product?.crossell?.result === '1' ? result?.product?.crossell?.comment : '',
						warning: result?.result?.warning?.result,
						limity: result?.product?.limity?.result === '1',
					},
					icons: Product_helpers.sort_icons(result?.icon),
					benefits: Product_helpers.sort_benefits(result?.benefit),
					inputs: Product_helpers.product_form_formatter(result?.results_form?.comment, true, false),
					spoluucast_krytie: Product_helpers.product_form_formatter(result?.results_form?.comment, true, true),
				};

				// loading
				this.loading = false;
			},
			select_this_product()
			{
				// loading
				this.loading = true;

				// store selected product for later usage
				STATE_params().set_selected_product(
					this.product_id,
					this.p_data.button.next_form,
					this.p_data.button.next_contract,
					this.p_data.period
				);

				// select request and redirect
				// also saves selected_product into session
				Product_requests.select_product().then(
					(response) => {

						// error, doesnt happen
						if (response.result.response !== 'success') {
							return;
						}

						// success
						STATE_core().change_step(3);

					}
				);
			},
			redirect_to_product()
			{
				if (!this.p_data.button.next_redirect) {
					console.log(this.product_id + ' next_redirect is undefined');
					return;
				}

				// window.open('https://www.google.com/', '_blank').focus();
				window.open(this.p_data.button.next_redirect, '_blank').focus();
			},
			open_product_detail()
			{
				// getting documents actually requires selected product id
				STATE_params().set_selected_product(
					this.product_id,
					null,
					null,
					null
				);

				// modal needs some info for gethtml request
				STATE_modals().store_modal_data(
					{
						product_name: this.product_name,
						detail_template: this.p_data.product.detail_template
					}
				);

				// open modal, request starts on created()
				STATE_modals().open_it('Product_detail');
			},
			recalculate_product()
			{
				// take all product input ids + spoluucast_krytie id
				let input_keys = Object.keys(this.p_data.inputs).concat(Object.keys(this.p_data.spoluucast_krytie));

				// get form data
				let form_data = Functions.serialize_specific_inputs(input_keys);

				// launch calculate from step2
				this.$emit('recalculate_product', this.product_id, form_data);
			},
			get_price_ws_details()
			{
				if (!this.p_data.product.limity) {
					return;
				}
				this.loading = true;

				// product_template_identifier is conditional variable in limity template, they need it in call_params before sending template
				Session_data.set_data(
					{
						product_template_identifier: this.product_id,
					}
				).then(
					() => {
						this.loading = false;
						STATE_modals().open_it('Product_ws_details');
					}
				);
			},
		},
		computed: {
			show_loading_animation()
			{
				let index = this.being_calculated.indexOf(this.product_id);
				return this.being_calculated[index] || this.loading;
			},
			not_zeteo_product()
			{
				return !STATE_core().is_zeteo_skin();
			},
		},
	}
</script>

<style lang="scss">
	/* it cuts off spaces and make it too clustered */
	.bold_move_to_side b,
	.euro_move_to_side {
		padding-left: 3px;
	}

	.the_form_is_disabled {
		pointer-events: none;
		opacity: 0.7;
	}

	.price_has_details {
		cursor: pointer;
		.amount {
			transition: color 0.2s;
			color: var(--warning);
		}
		&:hover {
			.amount {
				color: var(--text);
			}
		}
	}
</style>
