<template>
	<div class="calc_title_area" data-cy="title">

		<!-- title -->
		<h2
			v-if="loaded === 0"
		>
			Hľadáme produkty...
		</h2>
		<h2
			v-else
		>
			Našli sme
			<span
				:title="'z ' + total + ' prepočítavaných'"
				class="text-accent"
			>
				{{ products_found }}
			</span>
		</h2>

		<!-- download / send email -->
		<Product_delivery />

	</div>
</template>

<script>
	import Product_delivery from '@/components/form_app/product/Product_delivery.vue';

	export default {
		props: {
			loaded: Number,
			total: Number,
		},
		components: {
			Product_delivery,
		},
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			products_found()
			{
				if (this.loaded === 1) {
					return this.loaded + ' produkt';
				}
				else if (this.loaded > 1 && this.loaded < 5) {
					return this.loaded + ' produkty';
				}
				else /* 0 or 5+ */ {
					return this.loaded + ' produktov';
				}
			},
		},
	}
</script>
