<template>
	<Checkbox			v-if="type === 'checkbox'"			:field_id="field_id" :field_data="field_data" />
	<Plaintext			v-if="type === 'plaintext'"			:field_id="field_id" :field_data="field_data" />
	<Radio				v-if="type === 'radio'"				:field_id="field_id" :field_data="field_data" />
	<Select				v-if="type === 'select'"			:field_id="field_id" :field_data="field_data" />
	<Text				v-if="type === 'text'"				:field_id="field_id" :field_data="field_data" />
	<Textarea			v-if="type === 'textarea'"			:field_id="field_id" :field_data="field_data" />
	<Checkbox_icon		v-if="type === 'checkbox_icon'"		:field_id="field_id" :field_data="field_data" />
	<Datepicker			v-if="type === 'date'"				:field_id="field_id" :field_data="field_data" />
	<Multiple			v-if="type === 'multiple'"			:field_id="field_id" :field_data="field_data" />
	<Plus_minus			v-if="type === 'plus_minus'"		:field_id="field_id" :field_data="field_data" />
	<Psc				v-if="type === 'psc_input'"			:field_id="field_id" :field_data="field_data" />
	<Scan_modal			v-if="type === 'scan_modal'"		:field_id="field_id" :field_data="field_data" />
	<Search				v-if="type === 'search'"			:field_id="field_id" :field_data="field_data" />
	<Select_searchable	v-if="type === 'select_searchable'"	:field_id="field_id" :field_data="field_data" />
	<Slider				v-if="type === 'slider'"			:field_id="field_id" :field_data="field_data" />
</template>

<script>
	import Checkbox from '@/components/forms/basic_inputs/Checkbox.vue';
	import Plaintext from '@/components/forms/basic_inputs/Plaintext.vue';
	import Radio from '@/components/forms/basic_inputs/Radio.vue';
	import Select from '@/components/forms/basic_inputs/Select.vue';
	import Text from '@/components/forms/basic_inputs/Text.vue';
	import Textarea from '@/components/forms/basic_inputs/Textarea.vue';

	import Checkbox_icon from '@/components/forms/advanced_inputs/Checkbox_icon.vue';
	import Datepicker from '@/components/forms/advanced_inputs/Datepicker.vue';
	import Multiple from '@/components/forms/advanced_inputs/Multiple.vue';
	import Plus_minus from '@/components/forms/advanced_inputs/Plus_minus.vue';
	import Psc from '@/components/forms/advanced_inputs/Psc.vue';
	import Scan_modal from '@/components/forms/advanced_inputs/Scan_modal.vue';
	import Search from '@/components/forms/advanced_inputs/Search.vue';
	import Select_searchable from '@/components/forms/advanced_inputs/Select_searchable.vue';
	import Slider from '@/components/forms/advanced_inputs/Slider.vue';

	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
			Checkbox,
			Option,
			Plaintext,
			Radio,
			Select,
			Text,
			Textarea,
			Checkbox_icon,
			Datepicker,
			Multiple,
			Plus_minus,
			Psc,
			Scan_modal,
			Search,
			Select_searchable,
			Slider,
		},
		data() {
			return {
				type: '',
			}
		},
		created() {
			// switch type based on settings for different input approach, even when type is actually same
			if (
				// plus_minus is actially text, but different html
				this.field_data.type === 'text'
				&&
				this.field_data.type_settings?.plus_minus === 'true'
			) {
				this.type = 'plus_minus';
			}
			else if (
				// checkbox with icon requires different html
				this.field_data.type === 'checkbox'
				&&
				this.field_data.type_settings?.checkbox_icon !== undefined
				&&
				this.field_data.type_settings?.checkbox_icon !== ''
			) {
				this.type = 'checkbox_icon';
			}
			else if (
				// upload: scan_modal uses text type, but has completelly different purpose and structure
				this.field_data.type === 'text'
				&&
				this.field_data.type_settings?.upload === 'scan_modal'
			) {
				this.type = 'scan_modal';
			}
			else if (
				// psc has text type, but lets use different component for it
				Functions.ends_with(this.field_id, 'psc_mesto')
			) {
				this.type = 'psc_input';
			}
			else if (
				// custom multiple select input
				this.field_data.type === 'select'
				&&
				this.field_data.type_settings?.select_type === 'multiple'
			) {
				this.type = 'multiple';
			}
			else if (
				// former select2
				this.field_data.type === 'select'
				&&
				(
					Functions.ends_with(this.field_id, 'auto_znacka')
					||
					Functions.ends_with(this.field_id, 'auto_model')
					||
					Functions.ends_with(this.field_id, 'auto_vybava')
				)
				// used to be set in type_settings, but auto_model was missing this and i was lazy to tell them
				// this.field_data.type_settings.select_type === 'searchable'
			) {
				this.type = 'select_searchable';
			}
			else {
				// default and most often case
				this.type = this.field_data.type;
			}

			// alert if one them contains quotes, it breakes html attribute and they forget sometimes to use html entities
			// deprected, quotes are allowed in SPA, remove after while, 30.03.2023
			// if (
			// 	this.field_data.description.indexOf( '"' ) > -1 ||
			// 	this.field_data.placeholder.indexOf( '"' ) > -1
			// ) {
			// 	console.log( 'Double quotes detected in description or placeholder in field:', this.field_data );
			// }
		},
	}
</script>
