<template>
	<input
		:id="field_id"
		:name="field_id"
		v-model="value"
		@focus="open_calendar"
		@blur="close_calendar"
		@keyup.enter.prevent="close_calendar"
		ref="input_reference"
		class="form-control input-datepicker"
		type="text"
	>

	<div
		v-show="is_opened || debug_keep_open"
		class="datepicker"
		:class="aligned_to_top ? 'aligned_to_top' : ''"
	>
		<div
			class="calendar"
			@mouseenter="mouse_inside_calendar = true"
			@mouseleave="mouse_inside_calendar = false"
			@click="keep_focus"
		>
			<header>
				<span class="prev" @click="prev" >
					&lsaquo;
				</span>
				<span class="go_up" @click="go_up" >
					{{ up_the_text }}
				</span>
				<span class="next" @click="next" >
					&rsaquo;
				</span>
			</header>

			<!-- days -->
			<div
				v-if="calendar_type === 0"
			>
				<span
					v-for="weekday in construct_week"
					class="cell week_days"
				>
					{{ weekday }}
				</span>
				<span
					v-for="day in construct_days"
					@click="day_picked(day)"
					class="cell day"
					:class="[
						this_day(day) ? 'today' : '',
						day === 0 ? 'blank' : '',
						picked_day(day) ? 'picked' : '',
						is_before_disabled_day(day) ? 'date_disabled' : '',
						day < 0 || day > 100 ? 'other_month' : '',
					]"
				>
					{{
						day === 0 ? '' :
						day > 100 ? day - 100 :
						day < 0 ? Math.abs(day) : day
					}}
				</span>
			</div>

			<!-- months -->
			<div
				v-if="calendar_type === 1"
			>
				<span
					v-for="(number, month) in construct_months"
					@click="month_picked(number)"
					class="cell month"
					:class="[
						number === this_month ? 'today' : '',
						is_before_disabled_month(number) ? 'date_disabled' : '',
					]"
				>
					{{ month }}
				</span>
			</div>

			<!-- years -->
			<div
				v-if="calendar_type === 2"
			>
				<span
					v-for="year in construct_years"
					@click="year_picked(year)"
					class="cell year"
					:class="[
						year === this_year ? 'today' : '',
						is_before_disabled_year(year) ? 'date_disabled' : '',
					]"
				>
					{{ year }}
				</span>
			</div>

		</div>
	</div>

</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import Date_helpers from '@/components/forms/advanced_inputs/Date_helpers.js';
	import datepicker_range_mixin from '@/components/forms/dynamic_input_mixins/datepicker_range_mixin.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed', 'recalculate_product'],
		mixins: [datepicker_range_mixin],
		data() {
			return {
				is_opened: false,
				calendar_type: undefined, // 0 days, 1 months, 2 years, 3 decades
				mouse_inside_calendar: false,
				aligned_to_top: false,
				oldies: false,
				shown: {
					day: 0,
					month: 0,
					year: 0,
					decade: 0,
				},
				picked: {
					day: 0,
					month: 0,
					year: 0,
					decade: 0,
				},
				debug_keep_open: false,
			}
		},
		created() {
			// this two types show decades
			if (
				this.field_data?.type_settings?.calendar_type === 'years_first'
				||
				this.field_data?.type_settings?.calendar_type === 'no_value_show_oldies'
			)
			{
				this.calendar_type = 2;
			}
			else {
				// standard show days
				// this.field_data?.type_settings?.calendar_type === 'standard'
				this.calendar_type = 0;
			}
			// initialize on previous decade
			if (this.field_data?.type_settings?.calendar_type === 'no_value_show_oldies' && this.value === '') {
				this.oldies = true;
			}
			// dont remember if this was important
			this.picked = Date_helpers.default_shown(this.value);
		},
		mounted() {
		},
		methods: {
			prev()
			{
				if (this.calendar_type === 2)
				{
					this.shown.decade -= 30;
				}
				else if (this.calendar_type === 1)
				{
					this.shown.year--;
				}
				else if (this.calendar_type === 0)
				{
					if (this.shown.month === 1)
					{
						this.shown.month = 12;
						this.shown.year--;
					}
					else
					{
						this.shown.month--;
					}
				}
			},
			next()
			{
				if (this.calendar_type === 2)
				{
					this.shown.decade += 30;
				}
				else if (this.calendar_type === 1)
				{
					this.shown.year++;
				}
				else if (this.calendar_type === 0)
				{
					if (this.shown.month === 12)
					{
						this.shown.month = 1;
						this.shown.year++;
					}
					else
					{
						this.shown.month++;
					}
				}
			},
			go_up()
			{
				if (this.calendar_type === 2) {
					return;
				}
				this.calendar_type++;
			},
			day_picked(int)
			{
				if (int === 0) {
					return;
				}
				if (this.is_before_disabled_day(int)) {
					return;
				}
				if (int < 0) {
					this.prev();
					int = Math.abs(int);
				}
				if (int > 100) {
					this.next();
					int -= 100;
				}
				this.shown.day = int;
				this.picked = this.shown;
				this.update_input();
				this.is_opened = false;
			},
			month_picked(int)
			{
				if (this.is_before_disabled_month(int)) {
					return;
				}
				this.shown.month = int;
				this.calendar_type--;
			},
			year_picked(int)
			{
				if (this.is_before_disabled_year(int)) {
					return;
				}
				this.shown.year = int;
				this.calendar_type--;
			},
			update_input()
			{
				let day = this.picked.day.toString().padStart(2, '0');
				let month = this.picked.month.toString().padStart(2, '0');
				let year = this.picked.year.toString();
				this.value = day + '.' + month + '.' + year;
			},
			open_calendar()
			{
				let use_value = this.range_child_cant_start_before_parents_value();
				this.shown = Date_helpers.default_shown(use_value);
				if (this.oldies) {
					this.prev();
				}
				this.is_opened = true;
				this.check_the_space_bellow();
			},
			close_calendar(event)
			{
				if (
					event.relatedTarget === null
					&&
					this.mouse_inside_calendar
				) {
					// focus was lost by click event, inside of calendar
					return;
				}
				this.picked = Date_helpers.default_shown(this.value);
				this.update_input();
				this.calendar_type = 0;
				this.is_opened = false;
			},
			check_the_space_bellow()
			{
				let screen_size = window.innerHeight;
				let input_edge = this.$refs.input_reference.getBoundingClientRect().bottom;
				let calendar_size = 340;
				if (screen_size - input_edge < calendar_size) {
					this.aligned_to_top = true;
				} else {
					this.aligned_to_top = false;
				}
			},
			this_day(day)
			{
				let now = new Date();
				let compare_month;
				let compare_year;
				if (day < 0) {
					if (this.shown.month === 1) {
						compare_month = 12;
						compare_year = this.shown.year - 1;
					} else {
						compare_month = this.shown.month - 1;
						compare_year = this.shown.year;
					}
					day = Math.abs(day);
				} else if (day > 100) {
					if (this.shown.month === 12) {
						compare_month = 1;
						compare_year = this.shown.year + 1;
					} else {
						compare_month = this.shown.month + 1;
						compare_year = this.shown.year;
					}
					day = day - 100;
				} else {
					compare_month = this.shown.month;
					compare_year = this.shown.year;
				}
				if (
					now.getMonth() + 1 === compare_month
					&&
					now.getFullYear() === compare_year
				) {
					return now.getDate() === day;
				}
				return false;
			},
			picked_day(day)
			{
				if (this.value === '') {
					return false;
				}
				let compare_month;
				let compare_year;
				if (day < 0) {
					if (this.shown.month === 1) {
						compare_month = 12;
						compare_year = this.shown.year - 1;
					} else {
						compare_month = this.shown.month - 1;
						compare_year = this.shown.year;
					}
					day = Math.abs(day);
				} else if (day > 100) {
					if (this.shown.month === 12) {
						compare_month = 1;
						compare_year = this.shown.year + 1;
					} else {
						compare_month = this.shown.month + 1;
						compare_year = this.shown.year;
					}
					day = day - 100;
				} else {
					compare_month = this.shown.month;
					compare_year = this.shown.year;
				}
				if (
					this.picked.month === compare_month
					&&
					this.picked.year === compare_year
				) {
					return this.picked.day === day;
				}
				return false;
			},
		},
		computed: {
			construct_days()
			{
				return Date_helpers.construct_days(this.shown.year, this.shown.month);
			},
			construct_week()
			{
				return Date_helpers.construct_week();
			},
			construct_months()
			{
				return Date_helpers.construct_months();
			},
			construct_years()
			{
				return Date_helpers.construct_years(this.shown.decade);
			},
			this_month()
			{
				let now = new Date();
				if (
					now.getFullYear() === this.shown.year
				) {
					return now.getMonth() + 1;
				}
				return null;
			},
			this_year()
			{
				return new Date().getFullYear();
			},
			up_the_text()
			{
				if (this.calendar_type === 0)
				{
					return Date_helpers.show_month_name(this.shown.month) + ' ' + this.shown.year;
				}
				else if (this.calendar_type === 1)
				{
					return this.shown.year;
				}
				else if (this.calendar_type === 2)
				{
					return this.shown.decade + ' - ' + (this.shown.decade + 29);
				}
				else
				{
					return 'should not occur';
				}
			},
			value:
			{
				get() {
					if (this.emit_name !== undefined) {
						return STATE_params().get_user_input(this.field_id);
					}
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
					if (this.emit_name !== undefined) {
						this.$emit(this.emit_name);
					}
				},
			},
		},
		watch: {
		},
	}
</script>
