<template>
	<!-- photo taken -->
	<div class="contentarea_video">

		<!-- live video feedback -->
		<div
			v-show="!show_result"
			class="scan_camera"
		>
			<!-- direct result from camera -->
			<video
				ref="video"
				class="scan_video"
			>
				Video stream not available.
			</video>
			<!-- action button -->
			<button
				ref="button"
				class="scan_button btn btn-primary"
			>
				Odfotiť
			</button>
		</div>

		<!-- canvas temp storing video -->
		<canvas
			ref="canvas"
			class="scan_canvas"
		></canvas>

		<!-- final result -->
		<div
			v-show="show_result"
			class="scan_output"
		>
			<img
				ref="photo"
				class="scan_photo"
				alt="The screen capture will appear in this box."
			>
		</div>

	</div>
</template>

<script>
	export default {
		props: {
			position: Number, // 1 = front side, 2 = back side
		},
		components: {
		},
		emits: ['scan_feedback', 'scan_payload'],
		data() {
			return {
				show_result: false,
			}
		},
		created() {
			if (navigator.mediaDevices === undefined) {
				this.$emit('scan_feedback', 'Zariadenie pravdepodobne nemá kameru.');
				return;
			}

			this.$nextTick(
				() => {
					this.scan_stream();
				}
			);
		},
		methods: {
			scan_stream()
			{
				// The width and height of the captured photo. We will set the
				// width to the value defined here, but the height will be
				// calculated based on the aspect ratio of the input stream.

				var width = 550;    // We will scale the photo width to this (drag & drop area width)
				var height = 0;     // This will be computed based on the input stream

				// |streaming| indicates whether or not we're currently streaming
				// video from the camera. Obviously, we start at false.

				var streaming = false;

				// The various HTML elements we need to configure or control. These
				// will be set by the startup() function.

				var video = this.$refs.video;
				var canvas = this.$refs.canvas;
				var photo = this.$refs.photo;
				var button = this.$refs.button;

				function startup() {

					navigator.mediaDevices.getUserMedia({video: true, audio: false})
					.then(function(stream) {
						video.srcObject = stream;
						video.play();
					})
					.catch(function(err) {
						console.log("An error occurred: " + err);
					});

					video.addEventListener('canplay', function(ev){
						if (!streaming) {
						height = video.videoHeight / (video.videoWidth/width);

						// Firefox currently has a bug where the height can't be read from
						// the video, so we will make assumptions if this happens.

						if (isNaN(height)) {
							height = width / (4/3);
						}

						video.setAttribute('width', width);
						video.setAttribute('height', height);
						canvas.setAttribute('width', width);
						canvas.setAttribute('height', height);
						streaming = true;
						}
					}, false);

					button.addEventListener('click', function(ev){
						takepicture();
						display_captured_and_send_file();
						ev.preventDefault();
					}, false);

					clearphoto();
				}

				// Fill the photo with an indication that none has been
				// captured.

				function clearphoto() {
					var context = canvas.getContext('2d');
					context.fillStyle = "#AAA";
					context.fillRect(0, 0, canvas.width, canvas.height);

					var data = canvas.toDataURL('image/png');
					photo.setAttribute('src', data);
				}

				// Capture a photo by fetching the current contents of the video
				// and drawing it into a canvas, then converting that to a PNG
				// format data URL. By drawing it on an offscreen canvas and then
				// drawing that to the screen, we can change its size and/or apply
				// other changes before drawing it.

				function takepicture() {
					var context = canvas.getContext('2d');
					if (width && height) {
						canvas.width = width;
						canvas.height = height;
						context.drawImage(video, 0, 0, width, height);

						var data = canvas.toDataURL('image/png');
						photo.setAttribute('src', data);
					} else {
						clearphoto();
					}
				}

				// after capture display only captured
				// send it via same request as upload

				const display_captured_and_send_file = () => {
					this.show_result = true;
					this.$emit('scan_payload', photo.src, this.position);
					/* old app used to fill response in drop area
					credentials_scan.file = {
						valid: true,
						name: 'fotka',
						base: photo.src,
						url: photo.src
					};
					credentials_scan.send_scan_page();*/
				}

				// Set up our event listener to run the startup process
				// once loading is complete.
				// window.addEventListener('load', startup, false);

				startup();
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
