import STATE_core from '@/store/STATE_core.js';
import STATE_params from '@/store/STATE_params.js';
import Show_hide from '@/logic/Show_hide.js';

/**
 * Hide_hardcoded
*/
class Hide_hardcoded
{
	/**
	 * zp_person_boolean
	*/
	zp_person_boolean(input)
	{
		// run only in step3 in certain groups, others are always shown
		let group = STATE_core().product_group_id();
		if (
			STATE_core().current_step() !== 3
			||
			(
				group !== 3/*hypo*/
				&&
				group !== 4/*zp*/
				&&
				group !== 7/*cp*/
				&&
				group !== 73/*cudzi*/
			)
		) {
			return true;
		}

		// each group has own [selector, identifikator]
		let keyword = {
			3: ['hypo_ziadatel_pocet', '_osoba'],
			4: ['zp_poistenie_pocetosob', '_osoba'],
			7: ['osoby_pocet', '_poisteny'],
			73: ['cudzinci_osoby_pocet', '_poisteny'],
		};

		// important selector
		let pocet_osob = STATE_params().get_call_param(keyword[group][0]);
		// just a fallback
		if (pocet_osob === '') {
			return true;
		}
		pocet_osob = parseInt(pocet_osob, 10);

		// get person_index from id
		// id example: zp_osoba2_opravnenaosoba1_meno
		let id = Object.keys(input)[0];
		let start = id.indexOf(keyword[group][1]) + keyword[group][1].length;
		let person_index = id.substring(start, start + 2).replace('_', ''); // could be double digit
		person_index = parseInt(person_index, 10);

		// leave displayed inputs with person lower, same or without(NaN)
		if (isNaN(person_index) || person_index <= pocet_osob) {
			return true;
		}

		// hide inputs with person higher
		return false;
	}


	/**
	 * zp_checkboxes_boolean
	*/
	zp_checkboxes_boolean(input)
	{
		// run only in ZP and step3, others are always shown
		if (STATE_core().product_group_id() !== 4/*zp*/ || STATE_core().current_step() !== 3) {
			return true;
		}

		// run only for odpoved inputs
		this.id = Object.keys(input)[0];
		if (this.id.indexOf('_dotaznik_odpoved') === -1) {
			return true;
		}

		// get type
		this.type = input[this.id].type;

		// get product_prefix from id
		let end = this.id.indexOf('_osoba');
		this.product_prefix = this.id.substring(0, end);

		// get person_index from id
		let start = this.id.indexOf('_osoba') + '_osoba'.length;
		this.person_index = this.id.substring(start, start + 1);

		// get block_number from id
		start = this.id.indexOf('_dotaznik_odpoved') + '_dotaznik_odpoved'.length;
		this.block_number = this.id.substring(start, start + 2); // last 2 digits
		this.block_number = this.block_number.replace('_', ''); // but could be one digit ending with underscore
		this.block_number = parseInt(this.block_number, 10); // has to be INT, will be compared

		// selected product
		let contract = STATE_params().get_selected_product().next_contract;

		// Allianz + NN
		if (contract === 'view_zp_allianz' || contract === 'view_zp_nn') {
			return this.display_blocks_under_this_checkbox();
		}

		// Axa (Uniqa)
		if (contract === 'view_zp_axa') {
			return this.display_blocks_by_checkbox_count();
		}

		// other products are not affected
		return true;
	}


	/**
	 * display_blocks_under_this_checkbox
	*/
	display_blocks_under_this_checkbox()
	{
		// there are blocks of inputs, one parent question checkbox, when checked few answer inputs are shown
		// zp_allianz_osoba1_dotaznik_otazka6
		// zp_allianz_osoba1_dotaznik_odpoved6_1
		// zp_allianz_osoba1_dotaznik_odpoved6_2
		// zp_allianz_osoba1_dotaznik_odpoved6_3
		// {product_prefix}_osoba{person_index}_dotaznik_{otazka/odpoved}{block_number}_{odpoved_order}

		// the target id
		let target_id = this.product_prefix + '_osoba' + this.person_index + '_dotaznik_otazka' + this.block_number;

		// get targets value
		let checkbox_value = STATE_params().get_form_value(target_id);

		// if checked, show odpoved
		if (checkbox_value === '1') {
			return true;
		}

		// otherwise hidden, and value has to be purged
		Show_hide.purge_helper(this.type, this.id);
		return false;
	}


	/**
	 * display_blocks_by_checkbox_count
	*/
	display_blocks_by_checkbox_count()
	{
		// each checked question checkbox shows one answer input, count checked and show that many answer inputs
		// zp_axa_osoba1_dotaznik_otazka3e
		// zp_axa_osoba1_dotaznik_odpoved1_1
		// zp_axa_osoba1_dotaznik_odpoved1_2
		// zp_axa_osoba1_dotaznik_odpoved1_3
		// {product_prefix}_osoba{person_index}_dotaznik_{otazka/odpoved}{block_number}{?block_letter?}_{odpoved_order}

		// the target id
		let target_id = '_osoba' + this.person_index + '_dotaznik_otazka';

		// get all inputs
		let inputs = STATE_params().all_form_values();

		// count checked occurences
		let checked = 0;

		for (let key in inputs) { if (!inputs.hasOwnProperty(key)) { continue; }
			// skip not-questions and different persons
			if (key.indexOf(target_id) === -1) {
				continue;
			}
			// count checked
			if (inputs[key] === '1') {
				checked++;
			}
		}

		// show current block if its lower than checked count
		if (this.block_number <= checked) {
			return true;
		}

		// otherwise hidden, and value has to be purged
		Show_hide.purge_helper(this.type, this.id);
		return false;
	}
}

export default new Hide_hardcoded;
