import STATE_core from '@/store/STATE_core.js';

/**
 * Constants
*/
class Constants
{
	/**
	 * email_address
	*/
	email_address()
	{
		if (STATE_core().is_partners_skin()) {
			return 'poistenie@partnersgroup.sk';
		}
		return 'najpoistenie@najpoistenie.sk';
	}


	/**
	 * phone_number
	*/
	phone_number(tech_support)
	{
		if (tech_support) {
			// technical support for agents
			return '+421 905 497 554';
		}
		// common public users
		return '+421 917 535 773';
	}


	/**
	 * group_names
	*/
	group_names()
	{
		return {
			1: 'Povinné zmluvné poistenie',
			2: 'Havarijné poistenie',
			3: 'Hypotekárny úver',
			4: 'Životné poistenie',
			5: 'GAP poistenie finančnej straty',
			6: 'Investície',
			7: 'Cestovné poistenie',
			8: 'Poistenie zodpovednosti zamestnanca',
			9: 'Poistenie majetku - byt, dom, domácnosť',
			10: 'Poistenie podnikateľov',
			72: 'Sedadlá',
			73: 'Zdravotné poistenie cudzincov',
			81: 'Zdravotné poistenie',
			82: 'Test',
			85: 'Starobné dôchodkové sporenie', // dss
			86: 'Doplnkové dôchodkové sporenie', // dds
		};
	}
}

export default new Constants;
