<template>
	<h4>Platba cez QR kód</h4>
	<div id="qr_container">
		<img
			:src="qr_code"
			class="qr_code"
		>
	</div>
</template>

<script>
	import Payment_requests from '@/logic/requests/Payment_requests.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
				base64: '',
			}
		},
		created() {
			// ask for QR code
			Payment_requests.generate_qr().then(
				(response) => {

					// fallback
					if (response.status !== 200) {
						return;
					}

					// success
					this.base64 = response.result.pbs;

				}
			);
		},
		methods: {
		},
		computed: {
			qr_code()
			{
				// transofrm base64 into image source
				return this.base64 ? 'data:image/png;base64,' + this.base64 : '';
			},
		},
		watch: {
		},
	}
</script>
