import STATE_params from '@/store/STATE_params.js';
import Functions from '@/logic/Functions.js';
import Search_requests from '@/logic/requests/Search_requests.js';

let mixin = {
	props: {
	},
	components: {
	},
	emits: [],
	data() {
		return {
			vybava_id: 'havarijne_auto_vybava', // this select has to be exception for show/hide and purge its value
			vybava_watch: [
				'havarijne_auto_znacka', 'havarijne_auto_model', 'havarijne_objem', 'havarijne_vykon', 'havarijne_palivo', 'havarijne_hmotnost', 'havarijne_rokvyroby', 'havarijne_auto_vin', 'havarijne_auto_prevodovka', 'havarijne_auto_registracia', 'havarijne_auto_pocet_sedadiel', 'havarijne_auto_karoseria',
			],
			vybava_date: 'havarijne_auto_registracia',
			vybava_required: ['havarijne_auto_znacka', 'havarijne_auto_model', 'havarijne_vykon', 'havarijne_auto_vin', 'havarijne_palivo'],
			vybava_optional: ['havarijne_objem', 'havarijne_hmotnost', 'havarijne_rokvyroby', 'havarijne_auto_prevodovka', 'havarijne_auto_registracia', 'havarijne_auto_pocet_sedadiel', 'havarijne_auto_karoseria'],
			vybava_additional_label: 'havarijne_auto_vybava_label',
			delay: 1000, // 1s
			holding: false,
			time: '', // timeout promise
		}
	},
	created() {
		this.request_vybava();
	},
	methods: {
		request_vybava()
		{
			// run only on vybava input
			if (this.cached_vybava === null) {
				return;
			}

			// exit if any required is missing
			let i, len = this.vybava_required.length;
			for (i = 0; i < len; i++) {
				if (this.cached_vybava[this.vybava_required[i]] === '') {
					return;
				}
			}

			// has to be clones so we take only values not reference
			let params = structuredClone(this.cached_vybava);

			// take all watched inputs, and delete empty optionals
			for (let key in params) { if (!params.hasOwnProperty(key)) { continue; }
				if (this.vybava_optional.indexOf(key) > -1 && (params[key] === '' || params[key] === undefined)) {
					delete params[key];
				}
			}

			// format date input
			if (params.hasOwnProperty(this.vybava_date)) {
				params[this.vybava_date] = Functions.format_car_request_date(params[this.vybava_date]);
			}

			// serialize for request
			params = Functions.serialize_object(params);

			// ask backend for car data
			Search_requests.car_valuation_options(params).then(
				(response) => {

					// reset options
					this.options = {};

					// received data
					let data = response.result.data;

					// format them for select
					for (let order in data) { if (!data.hasOwnProperty(order)) { continue; }

						// save to options
						this.options[order] = {
							value: data[order].havarijne_auto_vybava,
							label: data[order].label_havarijne_auto_vybava,
						};


						// options[order] = {
						// 	[data[order].havarijne_auto_vybava]: data[order].label_havarijne_auto_vybava,
						// };


						// set default value if received in session
						if (parseInt(this.options[order].value, 10) === parseInt(STATE_params().get_form_value(this.field_id), 10)) {
							this.displayed = this.options[order].label;
						}

					}

					// reset value if changed, does not reset on initial load from session
					let missing_value_in_enum = true;
					for (let order in this.options) { if (!this.options.hasOwnProperty(order)) { continue; }
						let key = this.options[order].label;
						// console.log(key, this.displayed);
						if (key === this.displayed + '') {
							missing_value_in_enum = false;
						}
					}
					if (missing_value_in_enum) {
						this.displayed = '';
					}

				}
			);
		},
	},
	computed: {
		cached_vybava()
		{
			if (this.field_id !== this.vybava_id) {
				return null;
			}
			let value = {};
			let i, len = this.vybava_watch.length;
			for (i = 0; i < len; i++) {
				let id = this.vybava_watch[i];
				value[id] = STATE_params().get_form_value(id);
			}
			return value;
		},
	},
	watch: {
		cached_vybava()
		{
			if (this.holding === true) {
				clearTimeout(this.time);
			}
			this.holding = true;
			this.time = setTimeout(
				() => {
					this.holding = false;
					this.request_vybava();
				},
				this.delay
			);
		},
	},
};

export default mixin;
