import STATE_params from '@/store/STATE_params.js';
import Reference_table from '@/logic/requests/Reference_table.js';

let mixin = {
	props: {
	},
	components: {
	},
	emits: [],
	data() {
		return {
			dynamic: {
				// reactive id: listener id,
				auto_model: 'auto_znacka',
				havarijne_auto_model: 'havarijne_auto_znacka',
				gap_auto_model: 'gap_auto_znacka',
				sedadla_auto_model: 'sedadla_auto_znacka',
			},
			additional_label: 'auto_model',
			label_name: '__input_value_label_auto_model',
		}
	},
	created() {
		this.request_dynamic_value();
	},
	methods: {
		request_dynamic_value()
		{
			// exit if not dynamic
			if (this.cached_dynamic_parent === null) {
				return;
			}

			// find label and run reference_table_get
			Reference_table.auto_model_table(this.dynamic[this.field_id], this.cached_dynamic_parent).then(
				(response) => {

					// fallback
					if (response.status !== 200) {
						return;
					}

					/*// parse right format for enum <- old way for simple select
					let data = response.result.data;
					let options = {};
					for (let order in data) { if (!data.hasOwnProperty(order)) { continue; }
						options[order] = {
							[data[order].id]: data[order].model,
						};
					}
					this.field_data.enum = options;*/

					// reset options
					this.options = {};

					// received data
					let data = response.result.data;

					// simpler format for options
					for (let order in data) { if (!data.hasOwnProperty(order)) { continue; }

						// save to options
						this.options[order] = {
							value: data[order].id,
							label: data[order].model,
						};

						// set default value if received in session
						if (parseInt(this.options[order].value, 10) === parseInt(STATE_params().get_form_value(this.field_id), 10)) {
							this.displayed = this.options[order].label;
						}

					}

					// console.log(this.options);

					// reset value if changed, does not reset on initial load from session
					let missing_value_in_enum = true;
					for (let order in this.options) { if (!this.options.hasOwnProperty(order)) { continue; }
						let key = this.options[order].label;
						// console.log(key, this.displayed);
						if (key === this.displayed + '') {
							missing_value_in_enum = false;
						}
					}
					if (missing_value_in_enum) {
						this.displayed = '';
					}

				}
			);
		},
	},
	computed: {
		cached_dynamic_parent()
		{
			// null if not dynamic
			if (!this.dynamic.hasOwnProperty(this.field_id)) {
				return null;
			}
			// return watched value
			return STATE_params().get_form_value(this.dynamic[this.field_id]);
		},
		cached_additional_label()
		{
			// null if not additional_label
			if (this.field_id !== this.additional_label) {
				return null;
			}
			// return displayed label
			return this.displayed;
		},
	},
	watch: {
		cached_dynamic_parent()
		{
			// reset value only on later user change, never on init load
			// this.value = ''; stop it, bugged on search input
			this.request_dynamic_value();
		},
		cached_additional_label()
		{
			if (this.cached_additional_label === null) {
				return;
			}
			// save model label in data
			for (let order in this.options) { if (!this.options.hasOwnProperty(order)) { continue; }
				let key = this.options[order].label;
				if (key === this.displayed) {
					STATE_params().set_form_value(this.label_name, this.options[order].label);
					break;
				}
			}
		},
	},
};

export default mixin;
