import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';
import STATE_error from '@/store/STATE_error.js';

/**
 * Confirm_requests
*/
class Confirm_requests
{
	/**
	 * confirm_session
	*/
	confirm_session(checkboxes)
	{
		let requestParams = {
			action_name: 'confirm',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/confirm/?' + Functions.serialize_object(checkboxes) + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {
				if (response.status === 408) {
					// when confirm in step4 request timeouts, we repeat getsessioninfo
					return this.repeat_getsessioninfo(0);
				}
				// normal, didnt timeout
				return response;
			}
		).catch((error) => { throw error; });
	}


	/**
	 * repeat_confirm
	*/
	repeat_confirm()
	{
		let requestParams = {
			action_name: 'confirm',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/confirm/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch((error) => { throw error; });
	}


	/**
	 * repeat_getsessioninfo
	*/
	async repeat_getsessioninfo(iteration)
	{
		iteration++;
		let max_loops = 10;
		let delay = 10; // 10s
		console.log('wait ' + delay + ' seconds then try getsessioninfo, iteration: ' + iteration + ' (max ' + max_loops + ')');

		await Functions.sleep(delay);

		let requestParams = {
			action_name: 'getsessioninfo',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getsessioninfo/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {
				// success
				if (response.result?.state === 100) {
					return response;
				}
				// reached maximum occurences, show error page where getsessioninfo will repeat again
				if (iteration >= max_loops) {
					let date = new Date().toLocaleString();
					let session = STATE_core().get_session_token();
					let step = STATE_core().current_step();
					STATE_error().set_last_request({}, requestParams, date, session, step, response);
					STATE_core().change_step(404);
					return {status: 408, action_name: 'getsessioninfo',}; // just to prevent error since promises are expecting response object
				}
				// still no success, repeat after delay
				return this.repeat_getsessioninfo(iteration);
			}
		).catch((error) => { throw error; });
	}
}

export default new Confirm_requests;
