import { defineStore } from 'pinia';

export default defineStore(
	'STATE_modals',
	{
		state: () => {
			return {
				open_by_name: null, // null when empty || string name, without validation
				temporary_data: {}, // helps sending data to modal
			};
		},
		getters: {
			// opened
			which_is_open()
			{
				return () => {
					// returns name of currently open modal, or null if none is open
					return this.open_by_name;
				}
			},
			is_loading()
			{
				return () => {
					return this.open_by_name === 'Loading';
				}
			},
			// data
			retrieve_modal_data()
			{
				return () => {
					return this.temporary_data;
				}
			},
		},
		actions: {
			// close
			close_them_all()
			{
				this.open_by_name = null;
			},
			// open
			open_it(modal_name)
			{
				this.open_by_name = modal_name;
			},
			// data
			store_modal_data(modal_data)
			{
				this.temporary_data = modal_data;
			},
		},
	}
);
