import STATE_core from '@/store/STATE_core.js';	

/**
 * Form_cache
*/
class Form_cache
{
	/**
	 * store_form_cache
	*/
	store_form_cache(is_fresh_session)
	{
		// run only if is_fresh_session and when refresh_seo_cache is needed
		if (
			!is_fresh_session
			||
			!STATE_core().refresh_seo()
		) {
			return;
		}

		// let it build
		setTimeout(
			() => {
				let payload = document.getElementById('osm_app').innerHTML;
				payload = encodeURIComponent(payload);
				this.send_html_cache(payload);
			},
			2000 // 2s
		);
	}


	/**
	 * send_html_cache
	*/
	send_html_cache(payload)
	{
		let body = 'action=' + 'form_seo_cache' + '&html=' + payload + '&group=' + STATE_core().product_group_id();

		let url = window.location.origin + '/wp-admin/admin-ajax.php';

		let headers = {
			method: 'POST',
			mode: 'cors',
			cache: 'no-cache',
			credentials: 'omit',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8mb4',
				'Accept': 'application/json'
			},
			body: body,
		};

		fetch(url, headers)
		.then(
			(response) => {
				return response.json();
			}
		).then(
			(result) => {
				if (result !== 'success') {
					console.log('Cache_form_placeholder failed to send request.');
				}
			}
		).catch(
			(error) => { console.log(error); }
		);
	}


	/**
	 * clear_form_cache
	*/
	clear_form_cache()
	{
		document.getElementById('osm_app').innerHTML = '';
	}
}

export default new Form_cache;
